import {RoundResult} from '../../ignition-interactive/slots/model/RoundResult';
import {container} from 'tsyringe';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import {Text, TextStyle} from 'pixi.js';
import Wallet from '../../ignition-interactive/slots/model/Wallet';
import { HoldAndSpinnerRoundResult } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';

export default function intializeMultiplier(reelIndex:number, symbolIndex:number , multiplierStyle:TextStyle):Text {
    const roundResult : RoundResult & HoldAndSpinnerRoundResult = container.resolve(SlotMachine).roundResult;

    let m:Text;
    const initMap = roundResult?.holdAndSpinnerMultiplierMap?.initializationMap
    if(initMap?.length){
        initMap.forEach(mp=>{
            if (mp.x == reelIndex && mp.y == symbolIndex) {
                const value:number = mp.multiplier * container.resolve(SlotMachine).totalBet;
                m = new Text(container.resolve(Wallet).getCurrencyValue(value), multiplierStyle);
                m.name = 'multiplier';
                m.pivot.x = m.width / 2
                m.pivot.y = m.height / 2;
                m.y = m.height / 2;
                if (!container.resolve(SlotMachine).currentSpinResult.freespins && !container.resolve(SlotMachine).currentSpinResult.bonus)
                    m.alpha = 0.5;


            }
        })

    }
    return m;
}