import AssetsManager from '../../../core/assets/AssetsManager';
import { Container, Text } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Button from '../../../core/view/ui/Button';
import ControlEvent from '../../../core/control/event/ControlEvent';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';
import { SlotGameEventExtension } from '../event/SlotGameEventExtension';
import { container } from 'tsyringe';
import Wallet from '../../../slots/model/Wallet';
import SlotMachine from '../../../slots/model/SlotMachine';
import { autoscaleText } from '../../../core/utils/TextUtils';
import { UpdateLayoutDescription } from '../../../core/view/UpdateLayoutDescription';
import Translation from '../../../core/translations/Translation';
import MultiShadowText from '../text/MultiShadowText';
import {FeatureTypes} from '../../model/FeatureTypes';
import GameService from '../../../../the-miner/services/GameService';

export default class PopupFeatureBuy extends Container {
    private tfMainText: Text;
    private tfAmount: Text|MultiShadowText;
    private tfTitle: Text;

    private btnStart: Button;
    private btnCancel: Button;

    private _type:FeatureTypes;
    private gs: GameService;
    private wallet: Wallet;
    private sm: SlotMachine;

    constructor(type:FeatureTypes) {
        super();
        LayoutBuilder.create(AssetsManager.layouts.get('PopupFeatureBuy'), this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });
        this.type = type;

        if (this['tfMainText']) {
            this['tfMainText'].style.align = 'center';
        }

        this.tfTitle.style.align = 'center';

        autoscaleText(this.tfTitle, this.tfTitle.style.fontSize as number, 537, 244.5);


        this.gs = container.resolve(GameService);
        this.wallet = container.resolve(Wallet);
        this.sm = container.resolve(SlotMachine);

        const feature = this.gs.featureBuyConfig.config.find( feature => feature.RoundType == this._type);

        this.tfAmount.text = this.wallet.getCurrencyValue(
            feature.Multiplier * this.sm.totalBet * this.wallet.coinValue,
            true
        );
        if(this.tfAmount instanceof Text)
            autoscaleText(this.tfAmount, this.tfAmount.style.fontSize as number, 537, 120);
        else
            this.tfAmount.autoScale(this.tfAmount.style.fontSize, 537, 120)

        this.btnStart.on('pointerup', this.onBtnStart, this);

        this.btnCancel.on('pointerup', this.onBtnCancel, false);



        this.on('added', this.onAdded, this);

    }
    private set type(type){
        if (this._type === type)
            return
        this._type = type

        this.tfTitle.text = type == FeatureTypes.HOLD_AND_SPINNER? Translation.t('holdSpinner.popupTitle') : Translation.t('freeSpins.buy');

    }
    private onAdded(): void {

    }

    public updateLayout(desc: UpdateLayoutDescription) {
        const feature = this.gs.featureBuyConfig.config.find( feature => feature.RoundType == this._type);

        this.tfAmount.text = this.wallet.getCurrencyValue(
            feature.Multiplier * this.sm.totalBet * this.wallet.coinValue,
            true
        );
        if(this.tfAmount instanceof Text)
            autoscaleText(this.tfAmount, this.tfAmount.style.fontSize as number, 537, 120);
        else
            this.tfAmount.autoScale(this.tfAmount.style.fontSize, 537, 120)
    }

    protected customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            case 'Button':
                instance = new Button(le);
                break;
            case 'MultiShadowText':
                instance = new MultiShadowText(le);
                break;
        }
        return instance;
    }

    private onBtnStart(): void {
        new ControlEvent(UIPanelEvent.HIDE_POPUP).dispatch();
        if(this._type == FeatureTypes.FREESPIN) {
            this.gs.featureBuyConfig.currentRoundType = FeatureTypes.FREESPIN;
            new ControlEvent(SlotGameEventExtension.BUY_FREESPINS).dispatch();
        }
        else {
            this.gs.featureBuyConfig.currentRoundType = FeatureTypes.HOLD_AND_SPINNER;
            new ControlEvent(SlotGameEventExtension.BUY_HOLD_AND_SPINNER).dispatch();
        }
    }

    private onBtnCancel(): void {
        new ControlEvent(UIPanelEvent.HIDE_POPUP).dispatch();
    }
}
