import { container } from 'tsyringe';
import { PopupFactoryTokenConstants } from '../../ignition-interactive/common/tsyringe/PopupFactoryTokenConstants';
import { SpinePopupAnimationConfig } from '../../ignition-interactive/common/view/popup/SpinePopupAnimationConfig';
import { BigWinConfiguration, GrandWinConfiguration, MegaWinConfiguration, SuperWinConfiguration } from '../config/popups/PopupBigWinConfig';
import PopupCongratulationsConfig from '../config/popups/PopupCongratulationsConfig';
import PopupExtraFreespinConfig from '../config/popups/PopupExtraFreespinConfig';
import PopupFreespinsConfig from '../config/popups/PopupFreespinsConfig';
import PopupHoldAndSpinnerConfig from '../config/popups/PopupHoldAndSpinnerConfig';

container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_0,
    { useValue: BigWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_1,
    { useValue: MegaWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_2,
    { useValue: SuperWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_3,
    { useValue: GrandWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_FREESPINS_CONFIG,
    { useValue: PopupFreespinsConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_CONGRATULATIONS_CONFIG,
    { useValue: PopupCongratulationsConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_HOLD_AND_SPINNER_CONFIG,
    { useValue: PopupHoldAndSpinnerConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_EXTRA_FREESPIN_CONFIG,
    { useValue: PopupExtraFreespinConfig }
)