import ControlCommand from '../../../core/control/command/ControlCommand';
import ControlEvent from '../../../core/control/event/ControlEvent';
import {container} from 'tsyringe';
import SlotMachine from '../../../slots/model/SlotMachine';

export default class AdjustGameSpeedCommandOverride extends ControlCommand {

    public execute(event: ControlEvent): void {
        const sm: SlotMachine = container.resolve(SlotMachine);
        sm.currentGameSpeedLevel =  event?.data as number;

    }
}
