import { container } from 'tsyringe';
import Wallet from '../../../ignition-interactive/slots/model/Wallet';
import SlotMachine from '../../../ignition-interactive/slots/model/SlotMachine';
import ControlCommand from '../../../ignition-interactive/core/control/command/ControlCommand';
import {WalletEvent} from '../../../ignition-interactive/slots/model/event/WalletEvent';
import {SlotMachineState} from '../../../ignition-interactive/slots/model/SlotMachineState';
import {RoundResult} from '../../../ignition-interactive/slots/model/RoundResult';
import {SlotGameEvent} from '../../../ignition-interactive/slots/control/event/SlotGameEvent';
import Logger from '../../../ignition-interactive/core/utils/Logger';
import ControlEvent from '../../../ignition-interactive/core/control/event/ControlEvent';
import GameService from '../../services/GameService';
import {SlotGameEventExtension} from '../../../ignition-interactive/common/view/event/SlotGameEventExtension';
import {FeatureTypes} from '../../../ignition-interactive/common/model/FeatureTypes';


export default class BuyFeatureCommandOverride extends ControlCommand {

    public execute(event): void {
        const wallet: Wallet = container.resolve(Wallet);
        const sm: SlotMachine = container.resolve(SlotMachine);
        const gs: GameService = container.resolve(GameService);

        let featureRate;
        if(event.type == SlotGameEventExtension.BUY_FREESPINS){
            featureRate = gs.featureBuyConfig.config.find(feature => feature.RoundType == FeatureTypes.FREESPIN )
        }
        else {
            featureRate = gs.featureBuyConfig.config.find(feature => feature.RoundType == FeatureTypes.HOLD_AND_SPINNER )

        }

        const totalBet: number = featureRate.Multiplier * sm.totalBet * wallet.coinValue;
        if (wallet.balance < totalBet) {
            if (sm.autoplay.enabled) {
                sm.autoplay.spinsLeft = 0;
                sm.autoplay.enabled = false;
            }

            sm.currentState = SlotMachineState.IDLE;
            wallet.emit(WalletEvent.NOT_ENOUGH_BALANCE);
            return;
        }

        wallet.balance -= totalBet;

        sm.spinTimeLapsed = false;
        sm.reelsStarted = false;
        sm.stopRequested = false;
        sm.bigWinShown = false;

        sm.previousRoundResult = sm.roundResult;

        sm.roundResult.totalWinValue = 0;
        sm.roundResult = null;
        sm.currentState = SlotMachineState.SPINNING;

        gs.buyFeature()
            .then((roundResult: RoundResult) => {
                sm.roundResult = roundResult;
                new ControlEvent(SlotGameEvent.SPIN_STOP).dispatch();
            })
            .catch((e) => {
                Logger.error(e);
                sm.roundResult = sm.getDummyRoundResult(sm.previousRoundResult);
                wallet.balance += totalBet;
                sm.currentState = SlotMachineState.COMMUNICATION_ERROR;
            });
    }
}
