import { Container } from "pixi.js";
import LayoutElement from "../../../core/view/model/LayoutElement";
import LayoutBuilder from "../../../core/utils/LayoutBuilder";
import Button from "../../../core/view/ui/Button";
import SlotMachine from "../../../slots/model/SlotMachine";
import { container } from "tsyringe";
import SoundList from "../../../common/sound/SoundList";
import SoundManager from "../../../core/sound/SoundManager";
import GameService from "../../../../the-miner/services/GameService";

export default class UITurboSettings extends Container {
    private btnNormalTurbo: Button;
    private btnTurbo: Button;
    private btnSuperTurbo: Button;
    constructor(le: LayoutElement) {
        super();
        LayoutBuilder.create(le, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        this.btnNormalTurbo.on('pointerup', () => {
            this.onTurboBtnSelected(0);
        }, this);
        this.btnTurbo.on('pointerup', () => {
            this.onTurboBtnSelected(1);
        }, this);
        this.btnSuperTurbo.on('pointerup', () => {
            this.onTurboBtnSelected(2);
        }, this);
    }

    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            case 'Button':
                instance = new Button(le);
                break;
        }
        return instance;
    }
    protected onTurboBtnSelected(level: number) {
        const sm: SlotMachine = container.resolve(SlotMachine);
        sm.currentGameSpeedLevel=level;
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        const gs: GameService = container.resolve(GameService)
        gs.settings.quickSpin = level;
        gs.saveSettings(); 
    }
}