import { Container } from 'pixi.js';
import LayoutBuilder from '../../core/utils/LayoutBuilder';
import LayoutElement from '../../core/view/model/LayoutElement';
import Button from '../../core/view/ui/Button';

import { container } from 'tsyringe';
import ControlEvent from '../../core/control/event/ControlEvent';
import SoundManager from '../../core/sound/SoundManager';
import SlotMachine from '../../slots/model/SlotMachine';
import { SpeedSettingsStateEvent } from '../model/event/SpeedSettingsStateEvent';
import SpeedSettingsState from '../model/SpeedSettingsState';
import SoundList from '../sound/SoundList';

export default class TurboSelectChoice extends Container {
    private btnNormal:Button;
    private btnTurbo:Button;
    private btnSuperTurbo:Button;

    private _currentSelectedSpeed:number;

    private selectButtons:Button[] = [];
    private settingsState: SpeedSettingsState;

    constructor(le) {
        super();
        LayoutBuilder.create(le, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        this.settingsState = container.resolve(SpeedSettingsState);
        this.settingsState.on(SpeedSettingsStateEvent.CURRENT_SPEED_LEVEL_SET, (level) => this.updateView(level));

        this.selectButtons = [this.btnNormal, this.btnTurbo, this.btnSuperTurbo];
        this.selectButtons.forEach((btn, index) => btn.on('pointerup', () => this.onTurboSelect(index)));

    }
    public get currentSelectedSpeed(){
        return this._currentSelectedSpeed;
    }
    public onAdded(){
        this._currentSelectedSpeed = container.resolve(SlotMachine).currentGameSpeedLevel;
        this.updateView(this._currentSelectedSpeed);
    }

    private onTurboSelect(level:number){
        SoundManager.play(SoundList.UI_BUTTON_CLICK);

        new ControlEvent(SpeedSettingsStateEvent.CURRENT_SPEED_LEVEL_SET, level).dispatch();
        this._currentSelectedSpeed = level;
        this.updateView(level);
    }
    private updateView(level:number){
        this.selectButtons.forEach(btn => btn.scale.set(1));
        this.selectButtons[level].scale.set(1.45);
    }

    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        switch (le.customClass) {
            case 'Button':
                instance = new Button(le);
                break;
        }

        return instance;
    }
}
