import { container } from "tsyringe";
import ControlCommand from "../../../core/control/command/ControlCommand";
import ControlEvent from "../../../core/control/event/ControlEvent";
import SpeedSettingsState from "../../model/SpeedSettingsState";

export default class AdjustSpeedSettingsCommand extends ControlCommand {
    public execute(event: ControlEvent): void {
        const settings = container.resolve(SpeedSettingsState);
        settings.updateGameSpeedLevel(event.data as number);
    }
}