import { Point, Sprite } from 'pixi.js';
import { container, inject, injectable } from 'tsyringe';
import { HoldAndSpinnerRoundResult } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import { SlotGameEventExtension } from '../../ignition-interactive/common/view/event/SlotGameEventExtension';
import FeatureWinBanner from '../../ignition-interactive/common/view/features-components/FeatureWinBanner';
import HeartBanner from '../../ignition-interactive/common/view/features-components/HeartBanner';
import TotalWinFrame from '../../ignition-interactive/common/view/TotalWinFrame';
import UIPanelDesktop from '../../ignition-interactive/common/view/ui/UIPanelDesktop';
import UIPanelMobileVertical from '../../ignition-interactive/common/view/ui/UIPanelMobileVertical';
import AssetsManager from '../../ignition-interactive/core/assets/AssetsManager';
import ControlEvent from '../../ignition-interactive/core/control/event/ControlEvent';
import SoundManager from '../../ignition-interactive/core/sound/SoundManager';
import LayoutBuilder from '../../ignition-interactive/core/utils/LayoutBuilder';
import AdjustableLayoutContainer from '../../ignition-interactive/core/view/AdjustableLayoutContainer';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import { UpdateLayoutDescription } from '../../ignition-interactive/core/view/UpdateLayoutDescription';
import { SlotGameEvent } from '../../ignition-interactive/slots/control/event/SlotGameEvent';
import { SlotMachineEvent } from '../../ignition-interactive/slots/model/event/SlotMachineEvent';
import { CombinationWin, RoundResult, SpinResult } from '../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import { SlotMachineState } from '../../ignition-interactive/slots/model/SlotMachineState';
import Wallet from '../../ignition-interactive/slots/model/Wallet';
import { ReelsViewEvent } from '../../ignition-interactive/slots/view/event/ReelsViewEvent';
import ReelsView from '../../ignition-interactive/slots/view/ReelsView';
import { MultiplierEvent } from '../model/MultiplierEvent';
import GameService from '../services/GameService';
import SoundListExtended from '../sound/SoundListExtended';
import Character from './Character';
import FreespinMultiplierBoard from './FreespinMultiplierBoard';
import FreespinView from './FreespinView';
import HoldAndSpinnerView from './HoldAndSpinnerView';
import Logo from './Logo';
import MainScreenBackground, { BackgroundType } from './MainScreenBackground';
import { ReelHeader } from './ReelHeader';
import { SymbolsList } from './SymbolsList';

@injectable()
export default class MainGameScreen extends AdjustableLayoutContainer {
    private totalWinFrameBaseYPositions: {
        mobile: number;
    };

    private currentTheme:BackgroundType;

    private slotMachine: SlotMachine;

    private currentLineWinIndex: number = -1;
    private currentLineWinCycles: number = 0;
    private maxLineWinCycles: number = 2;

    // VIEWS
    public background: MainScreenBackground;
    public reels: ReelsView;
    public reelsBackground:Sprite;
    public uiPanelMobileVertical: UIPanelMobileVertical;
    public uiPanelDesktop: UIPanelDesktop;
    public totalWinFrame: TotalWinFrame;
    public featureWinBanner: FeatureWinBanner;

    public character: Character;
    public characterMobile: Character;
    public logoMobile:Logo;
    public logoDesktop:Logo;


    private reelHeader: ReelHeader;
    // View For HoldAndSpinner Multipliers
    public holdAndSpinnerView:HoldAndSpinnerView;
    // View For Freespin Multipliers
    public freespinView:FreespinView;


    public freespinMultiplier:FreespinMultiplierBoard;

    constructor(@inject(SlotMachine) slotMachine: SlotMachine) {
        super(AssetsManager.layouts.get('main-screen'));

        this.slotMachine = slotMachine;

        LayoutBuilder.create(this.layout, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        // Lock be default - first call to onSlotMachineStateChanged will update it
        this.uiPanelMobileVertical.lock();
        this.uiPanelDesktop.lock();

        this.holdAndSpinnerView.mobileBanner = this.totalWinFrame;
        this.totalWinFrameBaseYPositions = {
            mobile: this.totalWinFrame.y,
        };

        this.onSlotMachineStateChanged(this.slotMachine.currentState);
        this.slotMachine.on(SlotMachineEvent.STATE_CHANGED, this.onSlotMachineStateChanged, this);
        
        this.holdAndSpinnerView.on(MultiplierEvent.ON_MULTIPLIER_ANIMATION_END, this.featureWinBanner.setValue, this.featureWinBanner);
        this.holdAndSpinnerView.on(MultiplierEvent.ON_MULTIPLIER_ANIMATION_END, (val) =>{ 
            this.totalWinFrame.setValue(val, true);
            this.uiPanelDesktop.totalWinFrame.setValue(val, true);
        }, this);

        this.freespinView.on(MultiplierEvent.ON_ALL_MULTIPLIER_ANIMATION_END, this.freespinMultiplier.update, this.freespinMultiplier)


        this.on('added', this.onAdded, this);

        this.addChild(new Sprite(AssetsManager.textures.get('black-rect')));
    }

    // PRIVATE API
    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        switch (le.customClass) {
            case 'ReelsBackground':
                instance = new Sprite(AssetsManager.textures.get('reel'));
                break;
            case 'ReelsView':
                instance = new ReelsView(le, this.slotMachine.description.reels.regular, SymbolsList, {
                    winFrameAnimation: {
                        animationPrefix: 'win-frame-win',
                        fps: 30
                    },
                    anticipationTime: 1.5,
                    symbolSize: new Point(212, 212),
                    fallingCascade: true,
                });
                break;
            case 'UIPanelMobileVertical':
                instance = new UIPanelMobileVertical({
                    UIMainConfiguration: {
                        buttonSpinConfig: {
                            useRotationInStartAnimation: true,
                        },
                    },
                });
                break;
            case 'UIPanelDesktop':
                instance = new UIPanelDesktop({
                    UIMainConfiguration: {
                        buttonSpinConfig: {
                            useRotationInStartAnimation: true,
                        },
                    },
                });
                break;
            case 'MainScreenBackground':
                instance = new MainScreenBackground(this);
                break;
            case 'TotalWinFrame':
                instance = new TotalWinFrame(le);
                break;
            case 'TotalWinFrameDesktop':
                instance = new TotalWinFrame(le);
                break;
            case 'Character':
                instance = new Character();
                break
            case 'ReelHeader':
                instance = new ReelHeader(le);
                break;
            case 'Logo':
                instance = new Logo();
                break;
            case 'MultiplierFrame':
                instance = new FreespinMultiplierBoard(le);
                break;
            case 'FeatureWinBanner':
                instance = new FeatureWinBanner(le);
                break;
            case 'HoldAndSpinner':
                instance = new HoldAndSpinnerView(le, this.reels,this.featureWinBanner);
                break;
            case 'FreespinView':
                instance = new FreespinView(le, this.reels,this.freespinMultiplier);
                break;


        }

        return instance;
    }
    private moveTotalWinFrame(basePos:boolean){
    }

    public changeMainUI(theme:BackgroundType){
        this.currentTheme = theme;

        this.freespinMultiplier.alpha = theme == BackgroundType.FREEGAME?1:0;
        this.reelHeader.alpha = theme == BackgroundType.FREEGAME?0:1;
        this.logoDesktop.alpha = theme == BackgroundType.NORMAL?0:1;
        this.featureWinBanner.alpha = theme == BackgroundType.BONUS?1:0;
        this.characterMobile.alpha = theme == BackgroundType.FREEGAME?0:1;
        this.logoMobile.alpha = theme == BackgroundType.FREEGAME?0:1;


        this.uiPanelMobileVertical.setFeatureButtonsVisibility(theme);
        this.uiPanelDesktop.setFeatureButtonsVisibility(theme);

        if(theme == BackgroundType.FREEGAME || theme == BackgroundType.BONUS){
            this.moveTotalWinFrame(false)
        }
        else {
            this.moveTotalWinFrame(true);
        }

        if(theme == BackgroundType.BONUS)
            this.holdAndSpinnerView.initializeHoldAndSpinnerView(302,1000);
        else
            this.holdAndSpinnerView.clearHoldAndSpinnerView();


    }
    public updateLayout(desc: UpdateLayoutDescription) {
        super.updateLayout(desc);
        this.featureWinBanner.updateBackground(desc.orientation);


        this.holdAndSpinnerView.position = this.freespinView.position = this.reels
    }


    public onSlotMachineStateChanged(currentState: SlotMachineState): void {
        const sm: SlotMachine = this.slotMachine;
        const wallet: Wallet = container.resolve(Wallet);
        const gs: GameService = container.resolve(GameService);
        let gameSpeedLevel: number;
        switch (currentState) {
            case SlotMachineState.SPINNING:

                this.currentLineWinIndex = -1;
                this.currentLineWinCycles = 0;

                this.reels.off(ReelsViewEvent.WIN_ANIMATION_COMPLETE);

                if (sm.previousRoundResult?.complete) {
                    this.totalWinFrame.setValue(0);
                    this.uiPanelDesktop.totalWinFrame.setValue(0);

                }
                this.uiPanelMobileVertical.lock();
                this.uiPanelDesktop.lock();

                gameSpeedLevel = sm.stopRequested ? 1 : this.slotMachine.currentGameSpeedLevel;
                this.reels.spin(sm.currentGameSpeedLevel, {
                    id: SoundListExtended.UI_REEL_SPIN,
                    volume: 0.1,
                }, sm.options.gameSpeedLevels[sm.currentGameSpeedLevel]);
                break;
            case SlotMachineState.SPIN_END:

            case SlotMachineState.COMMUNICATION_ERROR:
                gameSpeedLevel = sm.stopRequested ? 1 : this.slotMachine.currentGameSpeedLevel;
                const spinResult: SpinResult = sm.currentSpinResult;
                const stopPromises: Promise<void>[] = [
                    this.reels.stop(
                        spinResult.result,
                        {
                            stopSoundData: {
                                id: SoundListExtended.UI_REEL_STOP,
                                volume: 0.2,
                            },
                            anticipationSoundData: {
                                id: SoundListExtended.REEL_ANTICIPATION,
                                volume: 0.6,
                            },
                        },
                        gameSpeedLevel,
                        this.getAnticipationReels(25,spinResult),
                        sm.options.gameSpeedLevels[sm.currentGameSpeedLevel]
                    ),
                ];
                Promise.all(stopPromises).then(() => {

                    if(sm.currentState != SlotMachineState.COMMUNICATION_ERROR) {

                        if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).hook){
                            this.freespinView.pullReel((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).hook).then(()=>{
                                if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).bazooka?.active){
                                    this.freespinView.explodeSymbols((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).bazooka).then(()=>{
                                        new ControlEvent(SlotGameEvent.REELS_STOPPED).dispatch();
                                    })
                                }
                                else
                                    new ControlEvent(SlotGameEvent.REELS_STOPPED).dispatch();



                            })
                        }
                        else {
                            if((sm.roundResult as RoundResult & HoldAndSpinnerRoundResult).bazooka?.active){
                                this.freespinView.explodeSymbols((sm.roundResult as RoundResult & HoldAndSpinnerRoundResult).bazooka).then(()=>{
                                    new ControlEvent(SlotGameEvent.REELS_STOPPED).dispatch();
                                })
                            }
                            else
                                new ControlEvent(SlotGameEvent.REELS_STOPPED).dispatch();
                        }



                    }
                });
                break;
            case SlotMachineState.BIG_WIN_FREE_SPIN:
                this.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                this.uiPanelDesktop.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                break;
            case SlotMachineState.SPIN_RESULT_MULTI_WIN:
                this.showMultiWin();
                if (sm.currentSpinResult.winValue > 0) {
                    if(!(container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinner) {
                        this.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                        this.uiPanelDesktop.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                    }
                    this.playMainElementsWinAnimations();
                }

                break;
            case SlotMachineState.SPIN_RESULT_SCATTER:
                new ControlEvent(SlotGameEvent.SCATTER_WIN_SHOWN).dispatch();
                break;
            case SlotMachineState.SPIN_RESULT_CASCADE:
                this.reels.cascade(sm.multiWinPattern(sm.previousSpinResult), sm.currentSpinResult.result)
                    .then(() => {
                        new ControlEvent(SlotGameEvent.CASCADE_WIN_SHOWN).dispatch();
                    });
                break;


            case SlotMachineState.BONUS_GAME:
                if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.toCollect){
                    this.holdAndSpinnerView.collectNewMultipliers((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap).then(() =>{
                        this.checkHoldAndSpinnerHealth();
                    })
                }
                else {
                    this.checkHoldAndSpinnerHealth();
                }
                break
            case SlotMachineState.FREE_SPINS_ROUND_END:
                this.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                this.uiPanelDesktop.totalWinFrame.setValue(sm.currentSpinResult.currentTotalWinValue, true);
                break;
            case SlotMachineState.IDLE:
                this.checkNextLineWin()
                this.uiPanelMobileVertical.unlock();
                this.uiPanelDesktop.unlock();
                break;


        }
    }

    private checkHoldAndSpinnerHealth():void{
        const sm:SlotMachine = container.resolve(SlotMachine);

        if((sm.roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinner?.remainingCount == 4 && this.uiPanelDesktop.heartBanner.prevHeartLevel != 4) {
            const heartsPromises:Promise<void>[] = [];
            [this.uiPanelDesktop.heartBanner, this.uiPanelMobileVertical.heartBanner].forEach((banner:HeartBanner) => {
                heartsPromises.push(banner.resetHearts(true));
            })
            Promise.all(heartsPromises).then(() => {
                [this.uiPanelDesktop.heartBanner, this.uiPanelMobileVertical.heartBanner].forEach((banner:HeartBanner) => {
                    banner.reduceHeart()
                });
                new ControlEvent(SlotGameEventExtension.HOLD_AND_SPINNER_START).dispatch();
            })
        }
        else if ((sm.roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinner?.remainingCount <= 4) {
            [this.uiPanelDesktop.heartBanner, this.uiPanelMobileVertical.heartBanner].forEach((banner:HeartBanner) => {
                banner.restore(true);
                banner.reduceHeart();
            })
            new ControlEvent(SlotGameEventExtension.HOLD_AND_SPINNER_START).dispatch();
        }
    }

    private showMultiWin(): void {
        const sm: SlotMachine = this.slotMachine;

        this.reels.once(ReelsViewEvent.WIN_ANIMATION_COMPLETE, () => {
            new ControlEvent(SlotGameEvent.MULTI_WIN_SHOWN).dispatch();
        });
        const winPattern: number[][] = this.slotMachine.multiWinPattern(this.slotMachine.currentSpinResult);
        SoundManager.play(SoundListExtended.WIN_FRAME);
        this.reels.animateWins(winPattern, 2, true);
        const lineWins = sm.currentSpinResult.win?.lines;


        if(lineWins.length == 1)
            sm.currentLineWin = lineWins[0].winValue


    }

    private checkNextLineWin(): void {
        const sm: SlotMachine = this.slotMachine;
        const combinationWins: CombinationWin[] = sm.currentSpinResult.win?.combinations;

        if (!combinationWins) {
            return;
        }

        if (this.currentLineWinIndex == -1) {
            this.currentLineWinIndex = 0;
        } else {
            this.currentLineWinIndex = (this.currentLineWinIndex + 1) % combinationWins.length;

            if (!this.currentLineWinIndex) {
                this.currentLineWinCycles++;
                if (this.currentLineWinCycles >= this.maxLineWinCycles) {
                    return;
                } else {
                    this.currentLineWinIndex = -1;
                    this.checkNextLineWin();
                    return;
                }
            }
        }
        this.showNextLineWin(combinationWins);
    }
    private showNextLineWin(combinationWins:CombinationWin[]){
        const sm: SlotMachine = this.slotMachine;

        const combinationWin: CombinationWin = combinationWins[this.currentLineWinIndex];
        sm.currentLineWin = combinationWin.wayPayout
        this.reels.once(ReelsViewEvent.WIN_ANIMATION_COMPLETE, () => {
            this.reels.off(ReelsViewEvent.WIN_ANIMATION_COMPLETE);

            new ControlEvent(SlotGameEvent.WINLINE_WIN_SHOWN, combinationWin).dispatch();

            if (sm.currentState == SlotMachineState.IDLE) {
                this.checkNextLineWin();
            }
        });

        this.reels.animateWins(combinationWin.pattern, 1, true);
    }

    private getWinningSymbols(output: number[][], winPattern: number[][]): Map<number, number> {
        const winingSymbols: Map<number, number> = new Map<number, number>();
        for (let i = 0; i < output.length; i++) {
            for (let j = 0; j < output[i].length; j++) {
                if (winPattern[i][j] == 1) {
                    if (winingSymbols.has(output[i][j])) {
                        const prevValue: number = winingSymbols.get(output[i][j]);
                        winingSymbols.set(output[i][j], prevValue + 1);
                    } else {
                        winingSymbols.set(output[i][j], 1);
                    }
                }
            }
        }

        return winingSymbols;
    }

    private getAnticipationReels(scatterId,spinResult: SpinResult): number[] {
        let scattersCount: number = 0;
        const anticipationReelIds: number[] = [];
        spinResult.result.forEach((reel:number[], id:number)=>{
            if(scattersCount >= 2)
                anticipationReelIds.push(id);

            if(reel.includes(scatterId))
                scattersCount++;
        });

        return anticipationReelIds;

    }
    private playMainElementsWinAnimations(){
        this.logoDesktop.playWinAnimation();
        this.logoMobile.playWinAnimation();
        this.character.playWinAnimation()
    }

    private onAdded(): void {


        //Restore previous freespin state
        if (this.slotMachine.roundResult.nextType === 10 || this.slotMachine.roundResult.nextType === 31) {
            this.totalWinFrame.setValue(this.slotMachine.currentSpinResult.currentTotalWinValue);
            //this.totalWinFrameDesktop.setValue(this.slotMachine.currentSpinResult.currentTotalWinValue);
            this.slotMachine.showFreeSpinsPopup = false;
            this.slotMachine.currentState = SlotMachineState.FREE_SPINS_ROUND_START;
            this.freespinMultiplier.restore();
        }
        else if (this.slotMachine.roundResult.nextType === 11) {
            this.slotMachine.currentState = SlotMachineState.SPIN_RESULT_BONUS_GAME;
            this.totalWinFrame.setValue(this.slotMachine.currentSpinResult.currentTotalWinValue);
            this.featureWinBanner.setValue(this.slotMachine.currentSpinResult.currentTotalWinValue);
            this.uiPanelDesktop.heartBanner.restore(true);
            this.uiPanelMobileVertical.heartBanner.restore(true);
        }
    }


}
