import {Container, Graphics} from 'pixi.js';
import LayoutBuilder from '../../ignition-interactive/core/utils/LayoutBuilder';
import {Tweener} from '../../ignition-interactive/core/tweener/ignitionTween';

export default class MultiplierCell  extends Container{
    private disabledView:Graphics;
    private activatedView:Graphics;

    constructor(le) {
        super();
        LayoutBuilder.create(le, this)
    }
    public deactivate():void{
        if(this.activatedView.visible == false){
            return
        }
        this.disabledView.visible = true;
        this.activatedView.visible = false;
    }
    public async activate(animate:boolean = true):Promise<void>{
        return new Promise(resolve => {
            if(this.activatedView.visible == true){
                resolve();
                return;
            }
            if(!animate){
                this.activatedView.visible = true;
                this.disabledView.visible = false;
                resolve();
                return;
            }
            Tweener.removeTweens(this.activatedView);
            Tweener.addTween(this.activatedView.scale, {
                onStart:()=>{
                    this.activatedView.visible = true;
                    this.disabledView.visible = false;
                },
                x:1.3,
                y:1.3,
                time:0.4,
                onComplete: () => {
                    Tweener.addTween(this.activatedView.scale, {
                        x:1,
                        y:1,
                        time:0.4,
                        onComplete:() => resolve()
                    })
                }
            })
        })

    }
}