import EventEmitter from 'eventemitter3';
import { Container, Sprite, Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import ControlEvent from '../../../core/control/event/ControlEvent';
import SoundManager from '../../../core/sound/SoundManager';
import Translation from '../../../core/translations/Translation';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Button from '../../../core/view/ui/Button';
import SlotMachine from '../../../slots/model/SlotMachine';
import { SlotMachineState } from '../../../slots/model/SlotMachineState';
import Wallet from '../../../slots/model/Wallet';
import { SlotMachineEvent } from '../../../slots/model/event/SlotMachineEvent';
import { WalletEvent } from '../../../slots/model/event/WalletEvent';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';
import { FeatureTypes } from '../../model/FeatureTypes';
import { PopupData, PopupType } from '../../model/PopupState';
import { GameServiceEvent } from '../../services/GameServiceEvent';
import SoundList from '../../sound/SoundList';
import { CommonTokenConstants } from '../../tsyringe/tokens/CommonTokenConstants';
import TextFit from '../TextFit';
import { multiStyledText } from '../text/TextFormat';
import GameService from '../../../../the-miner/services/GameService';

export class FreeSpinButton extends Button {

    private tfTitle: TextFit;
    private tfValue: TextFit;

    private icon?: Sprite;
    private tfValueDefaultX: number;

    private currentValue:number;
    private currentTheme:BackgroundType;


    constructor(l: LayoutElement) {
        super(l, (le) => {
            let instance: unknown = null;
            switch (le.customClass) {
                case 'TextFit':
                    instance = new TextFit(le)
                    break;
            }
            return instance;
        });
        const sm: SlotMachine = container.resolve(SlotMachine);
        const wallet: Wallet = container.resolve(Wallet);
        const gs: GameService = container.resolve(GameService)
        sm.on(SlotMachineEvent.STATE_CHANGED, this.onSlotMachineStateChanged, this);

        this.currentValue = (sm.currentSpinResult?.freespins?.remainingCount ?? 1) - 1;
        if(Number.isNaN(this.currentValue)) {
            this.currentValue = 0;
        }
        this.currentTheme = BackgroundType.NORMAL;

        this.icon = (<Container>this.normal).children.find((child) => child.name !== 'bcg' && child instanceof Sprite) as Sprite;
        

        this.tfTitle = this.normal['tfTitle'];
        this.tfValue = this.normal['tfValue'];
        this.tfValueDefaultX = this.tfValue.x;

        this.tfTitle.text.style.align = 'center';



        const feature = gs.featureBuyConfig.config.find( feature => feature.RoundType == FeatureTypes.FREESPIN);



        sm.on(SlotMachineEvent.BET_VALUE_CHANGED, () => {
            this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(feature.Multiplier * sm.totalBet * wallet.coinValue, true));
        }, this);

        wallet.on(WalletEvent.COIN_VALUE_CHANGED, () => {
            this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(feature.Multiplier * sm.totalBet * wallet.coinValue, true));
        }, this);

        (gs as unknown as EventEmitter).on(GameServiceEvent.DOUBLE_CHANCE_CHANGED, () => {
            this.setActive(!gs.doubleUpChance, !gs.doubleUpChance ? 1 : 0.4);
        }, this);

        this.on('pointerup', this.onClick, this);
        
        if(container.isRegistered(CommonTokenConstants.FREESPIN_MULTI_STYLED_TEXT)){
            const styleArr:Partial<TextStyle>[] = container.resolve(CommonTokenConstants.FREESPIN_MULTI_STYLED_TEXT);
            multiStyledText(this.tfTitle.text as Text, styleArr, ['Y',]);
        }


        // At this point SlotMachine, Wallet and GameService are fully initialized and contains valid data to be displayed
        this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(/*gs.featureBuyConfig.Rate * */sm.totalBet * wallet.coinValue, true));
    }

    public moreAwarded(){
        this.currentValue = container.resolve(SlotMachine).currentSpinResult?.freespins?.remainingCount ?? 0;
        if(Number.isNaN(this.currentValue)) {
            this.currentValue = 0;
        }
    }

    private onSlotMachineStateChanged(currentState: SlotMachineState): void {
        switch (currentState) {
            case SlotMachineState.SPINNING:
                if(this.currentTheme == BackgroundType.FREEGAME){
                    this.currentValue--;
                    this.setTexts(Translation.t('freeSpins.left'), (this.currentValue).toString(), false);
                }
                break;

        }
    }

    // onSlotMachineStateChanged doesn't synchronize with transition animation, so below function is used to change state during transition
    public changeState(theme :BackgroundType):void{
        const sm: SlotMachine = container.resolve(SlotMachine);
        const wallet: Wallet = container.resolve(Wallet);
        const gs: GameService = container.resolve(GameService);


        this.currentValue = sm.currentSpinResult?.freespins?.remainingCount ?? 0;
        this.currentTheme = theme;
        const feature = gs.featureBuyConfig.config.find( feature => feature.RoundType == FeatureTypes.FREESPIN);

        switch (theme){
            case BackgroundType.NORMAL:
                this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(feature.Multiplier * sm.totalBet * wallet.coinValue, true), true,true);
                if(this.icon) {
                    this.icon.alpha = 1;
                    this.tfValue.x = this.tfValueDefaultX;
                }
            break
            case BackgroundType.FREEGAME:
                if(sm.currentState == SlotMachineState.FREE_SPINS_ROUND_START)
                    this.setTexts(Translation.t('freeSpins.left'), (this.currentValue).toString(), false, true);
                //else if(sm.currentState == SlotMachineState.SPIN_RESULT_FREE_SPINS)
                  //  this.setTexts(Translation.t('freeSpins.left'), (sm.currentSpinResult.freespins.totalCount).toString(), false, true);

                if(this.icon) {
                    this.icon.alpha = 0;
                    this.tfValue.x = (<Container>this.normal).width * 0.5;
                }
            break
        }
    }

    private onClick(): void {
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        const data: PopupData = {
            type: PopupType.FREESPIN_BUY,
            hideOnClick: false,
            duration: -1,
            callbacks: null
        }
        new ControlEvent(UIPanelEvent.SHOW_POPUP, data).dispatch();
    }

    public setTexts(title: string, value: string, isActive: boolean = true, isNotAlpha: boolean = true): void {
        this.tfTitle.text.text = title;
        this.tfValue.text.text = value;


        this.setActive(isActive);
        this.updateLayout();

        isNotAlpha ? this.alpha = 1 : this.alpha = 0.4
    }

    private setActive(isActive: boolean, alpha?:number): void {
        this.enabled = isActive;

        if(alpha)
            this.alpha = alpha
    }

    public updateLayout() {
        this.tfTitle.updateLayout();
        this.tfValue.updateLayout();
    }
}
enum BackgroundType {
    NORMAL = 'main-screen-background',
    FREEGAME = 'freegame-screen-background',
    BONUS = 'bonus-screen'
}
