import { Spine } from '@pixi-spine/all-4.1'
import { Graphics, Point, Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import {
    BazookaItem,
    HoldAndSpinnerRoundResult,
    HookItem,
    MultiplierMap,
    SymbolsBaseMultiplierMap
} from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import AssetsManager from '../../ignition-interactive/core/assets/AssetsManager';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import { autoscaleText } from '../../ignition-interactive/core/utils/TextUtils';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import { RoundResult, } from '../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import Wallet from '../../ignition-interactive/slots/model/Wallet';
import ReelsView from '../../ignition-interactive/slots/view/ReelsView';
import ReelView from '../../ignition-interactive/slots/view/ReelView';
import SymbolView from '../../ignition-interactive/slots/view/SymbolView';
import ChosenSymbolView from '../extends/ChosenSymbolView';
import WildSymbolView from '../extends/WildSymbolView';
import { Transformation } from '../model/Transformation';
import FeatureView from './FeatureView';
import FreespinMultiplierBoard from './FreespinMultiplierBoard';
import { SymbolsList } from './SymbolsList';


export default class FreespinView extends FeatureView{
    private currentSpinWildSummary:number = 0;

    private hooks:Spine[] = [];
    private hookMask:Graphics;

    private explosionsSpines:Spine[] = [];

    constructor(le:LayoutElement, reels:ReelsView, banner:FreespinMultiplierBoard) {
        super(le,reels,banner);



        this.initializeHooksForEachReel();
        this.initializeExplosions();

    }
    private initializeExplosions(){

        // create explosion spines for each symbol and do not reposition them, because update layout is not called at this moment in constructor
        // reposition should be done in first explosion animation
        const asset = AssetsManager.spine.get('disappear');
        this.reels.getReelViews().forEach(reel => {
            reel.getVisibleSymbols().forEach(() => {

                const ex:Spine = new Spine(asset);
                this.addChild(ex);
                this.explosionsSpines.push(ex);

                this.resetSpine(ex);

            })
        })
    }
    private initializeHooksForEachReel(){

        // hooks can appears on each reel and random amount at the same time
        const hookAsset = AssetsManager.spine.get('hook');
        const hookYOffsetAdjustment:number = 225

        // for each reel create hook, add to this view and reposition
        this.reels.getReelViews().forEach((reel:ReelView) => {

            const hook:Spine = new Spine(hookAsset);
            hook.mask = this.hookMask;

            this.hooks.push(hook);
            this.addChild(hook);


            hook.y = -hookYOffsetAdjustment;
            hook.x = reel.x + reel.width/2;

            this.resetSpine(hook);
        })
    }

    public async collectMultiplier(multiplierMap:SymbolsBaseMultiplierMap):Promise<void> {

        // reset multiplier value of wild in new spin
        this.currentSpinWildSummary = 0;
        const moveAnimations:Promise<void>[] = [];
        // get wild from reel
        const wild:WildSymbolView= this.reels.getReelViews()[multiplierMap.wildPosition.x].getVisibleSymbols()[multiplierMap.wildPosition.y] as WildSymbolView;

        // send each multiplier from reel to wild
        multiplierMap.featureSymbolMultiplierMap.forEach((symbol:MultiplierMap, index):void => {
            const multiplierSymbol:ChosenSymbolView = this.reels.getReelViews()[symbol.x].getVisibleSymbols()[symbol.y] as ChosenSymbolView;
            const multiplier = multiplierSymbol.multiplier;

            this.animateSymbol(multiplierSymbol,'win',index);
            if(multiplier){
                moveAnimations.push(
                    this.moveMultiplier(multiplier as Text, wild, 30,index,wild, multiplierSymbol),
                )
            }
        })


        await Promise.all(moveAnimations);


        //if there is feature multiplier on wild and there is summary multiplied value then multiply it by summary values
        if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier > 1 && wild.multiplier) {
            const wildM =  wild.multiplier;
            
            await this.moveMultiplier(
                wild.multiplier,
                wild.summaryMultiplier
            );

            await this.multiplyWild(wild, wildM)
        }
        // Instead of emitter
        // send summary multiplier to freespin board
        if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.collectedCount <= 12 && wild.summaryMultiplier) {
            const nextCell = (this.banner as FreespinMultiplierBoard).getNextCell();
            if (nextCell) {
                await this.moveMultiplier(
                    wild.summaryMultiplier,
                    nextCell
                );
            }
        }

        // update freespin board
        await (this.banner as FreespinMultiplierBoard).update();
    }

    private async multiplyWild(wild:WildSymbolView, wildM):Promise<void> {

        // Check if summaryMultiplier exists before proceeding
        if (!wild.summaryMultiplier) {
            console.warn('FreespinView.multiplyWild: wild.summaryMultiplier is undefined, cannot multiply wild');
            return;
        }

        // Check if wildM exists and has text property
        if (!wildM || !wildM.text) {
            console.warn('FreespinView.multiplyWild: wildM is undefined or does not have text property');
            return;
        }

        const multiplierSummary:string = wild.summaryMultiplier.text;
        const summary:number =Number(parseFloat(multiplierSummary.slice(0,-1).replace('.', '').replace(',','.')).toFixed(2))

        this.currentSpinWildSummary = summary * parseFloat(wildM.text.slice(1));
        wild.summaryMultiplier.text = `${this.currentSpinWildSummary.toLocaleString('de-DE')}${Wallet.currency.isoCode}`;
    }



    private async handleWildSummary(text,style, wild:WildSymbolView,symbol:SymbolView){
        // after sending multiplier to wild, recreate it on the same symbol
        await this.createMultiplier(symbol,text,style);

        if(this.currentSpinWildSummary == 0 && !wild.summaryMultiplier){

            // if wild don't have multiplier create new one
            this.currentSpinWildSummary += parseFloat(text.slice(0,-1).replace('.', '').replace(',','.'));
            this.currentSpinWildSummary = Number(this.currentSpinWildSummary.toFixed(2))
            wild.summaryMultiplier = await this.createMultiplier(wild, `${this.currentSpinWildSummary.toLocaleString('de-DE')}${Wallet.currency.isoCode}`, style);
        }
        else if(this.currentSpinWildSummary > 0){

            // if wild has multiplier update the value
            this.currentSpinWildSummary += parseFloat(text.slice(0,-1).replace('.', '').replace(',','.'));
            this.currentSpinWildSummary = Number(this.currentSpinWildSummary.toFixed(2));

            wild.summaryMultiplier.text = `${this.currentSpinWildSummary.toLocaleString('de-DE')}${Wallet.currency.isoCode}`;
        }

        // animate wild
        this.animateSymbol(wild,'collect');
    }

    private async moveMultiplier(multiplier:Text, finalPos, offsetY:number = 0,delay:number = 0, wild?:WildSymbolView, multiplierSymbol?:ChosenSymbolView):Promise<void>{
        // detach multiplier from symbolView
        const multiplierOldPos: Point = multiplier.getGlobalPosition();
        multiplier.setParent(this);
        multiplier.position = this.toLocal(multiplierOldPos);

        // Check if finalPos is valid before proceeding
        if (!finalPos || typeof finalPos.getGlobalPosition !== 'function') {
            console.warn('FreespinView.moveMultiplier: finalPos is undefined or does not have getGlobalPosition method');
            const text :string = multiplier.text;
            const style :TextStyle = multiplier.style;
            multiplier.destroy();
            
            if(wild)
                await this.handleWildSummary(text,style,wild,multiplierSymbol);
            return;
        }

        const time: number = 0.6;
        const point:Point  = finalPos.getGlobalPosition();
        point.y += offsetY;
        const transformation: Transformation = {
            translation: this.toLocal(point),
            scale: new Point(1.8, 1.8)
        };
        // send multiplier to final Pos
        await this._animationMoveStrategy.animate(multiplier,time,delay,transformation,!wild);

        const text :string = multiplier.text;
        const style :TextStyle = multiplier.style;
        multiplier.destroy()

        if(wild)
            await this.handleWildSummary(text,style,wild,multiplierSymbol)

    }


    public async pullReel(response:HookItem):Promise<void>{
        const delayForEachHookPull:number = 750;
        const fitPullingToHookAnimationTime:number = 2300;

        const pullSpeed:number = 0.42;
        const offsetYForReelMask:number = 100;

        return new Promise((resolve)=> {
            response.result.forEach((item, index) => {
                const currentReel: ReelView = this.reels.getReelViews()[item.reelId];
                const currentHook: Spine = this.hooks[item.reelId];


                currentHook.state.addListener({
                    start: () => {
                        currentHook.visible = true;
                        setTimeout(() => {
                            // after timeout -> pull reel
                            currentReel.pullReel(item.newReel, pullSpeed, offsetYForReelMask)

                        }, fitPullingToHookAnimationTime)
                    },
                    complete: () => {
                        this.resetSpine(currentHook);
                        if(index == response.result.length-1)
                            resolve();
                    }
                })
                setTimeout(() => {
                    currentHook.state.setAnimation(0, 'active', false);

                }, index * delayForEachHookPull)
            })
        })

    }


    //instead of bazooka animation we have explosion animation
    public async explodeSymbols(result:BazookaItem):Promise<void>{

        const reelSymbolsMap:SymbolView[] = [];


        // animations of explosions should be played from left to right in each row
        // but symbols are indexed by columns
        const reelsCount:number = this.reels.getReelViews().length;
        const rowsCount:number =  this.reels.getReelViews()[0].getVisibleSymbols().length;
        const iteration:number = reelsCount * rowsCount;

        let wildSymbolId:number;

        for (let i:number = 0; i < iteration ; i++) {
            const currentSymbol:SymbolView = this.reels.getReelViews()[i % reelsCount].getVisibleSymbols()[Math.floor(i/reelsCount)]
            reelSymbolsMap.push(currentSymbol);

            // get the id of wild symbol to avoid animating explosion on it
            if(currentSymbol.data.id == 302)
                wildSymbolId = i;
        }



        // reposition explosions
        reelSymbolsMap.forEach((symbol:SymbolView, index:number) => {
            const ex:Spine = this.explosionsSpines[index];

            // don't reposition spines each bazooka feature if they already have good positions
            if(ex.position.x != 0 && ex.position.y != 0)
                return

            const point:Point = symbol.getGlobalPosition();
            ex.position = this.toLocal(point);
        })

        return new Promise((resolve)=>{
            // animate each explosion, except the one which is over wild symbol
            this.explosionsSpines.forEach((ex:Spine,index:number) => {
                if(index == wildSymbolId)
                    return

                Tweener.addCaller(this,{
                count:1,
                delay:0.12 * index,
                    onComplete:() =>{
                        ex.state.addListener({
                            complete:() => {
                                this.resetSpine(ex);

                                if(index == this.explosionsSpines.length-1)
                                    resolve();
                            }
                        })


                        ex.visible = true;
                        ex.state.setAnimation(0,'disappear',false);

                        // swap all the symbols from response


                        const oldID:number =container.resolve(SlotMachine).currentSpinResult.result[index % reelsCount][Math.floor(index/reelsCount)]
                        const newID:number   = result.R[index % reelsCount][Math.floor(index/reelsCount)]
                        if(oldID !== newID){
                            const symbolData = SymbolsList.find(s => s.id === newID)
                            let nS:SymbolView;
                            if(symbolData.specialViewClass){
                                nS = new symbolData.specialViewClass(symbolData,index % reelsCount,Math.floor(index/reelsCount) );
                            }
                            else {
                                nS = new SymbolView(symbolData);
                            }
                            this.reels.getReelViews()[index % reelsCount].swapSymbol(Math.floor(index/reelsCount),nS);
                        }


                    },
                })
            })
        })
    }

    public override createMultiplier(symbol: SymbolView, value: string, style: TextStyle): Text {
        const text = super.createMultiplier(symbol, value, style);
        
        Tweener.pauseTweens(text);
        const scaleX = text.scale.x;
        const scaleY = text.scale.y;
        text.scale.set(1);
        autoscaleText(text, 60, symbol.spineAnim.width, symbol.spineAnim.height);
        text.pivot.set(text.width * 0.5, 0);
        text.scale.set(scaleX, scaleY)
        Tweener.resumeTweens(text);

        return text;
    }
}