import { Slider, SliderOptions } from "@pixi/ui";
import { BitmapText, Text } from "pixi.js";
import Translation from "../../../ignition-interactive/core/translations/Translation";

export class SliderMinMaxText extends Slider {
    private minText: BitmapText;
    private maxText: BitmapText;

    constructor(options: SliderOptions) {
        super(options);
        
        // Create min and max text labels
        this.minText = new BitmapText(Translation.t("autoSpin.tfMin"), {
            fontName: 'Alexandria',
            fontSize: 16,
            align: 'center'
        });
        this.minText.anchor.set(0.5, 0.5);
        
        this.maxText = new BitmapText(Translation.t("autoSpin.tfMax"), {
            fontName: 'Alexandria',
            fontSize: 16,
            align: 'center'
        });
        this.maxText.anchor.set(0.5, 0.5);

        // Position min text on the left
        this.minText.position.set(- 24, 12);

        // Position max text on the right
        this.maxText.position.set(this.width + 24, 12);

        // Add texts to the slider
        this.addChild(this.minText);
        this.addChild(this.maxText);
    }
}