import AssetsManager from '../../ignition-interactive/core/assets/AssetsManager';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import {Spine, TrackEntry} from '@pixi-spine/all-4.1'
import {Container} from 'pixi.js';

export default class Logo extends Container{
    private spine: Spine;
    constructor() {
        super();
        const asset = AssetsManager.spine.get('logo');
        this.spine = new Spine(asset);
        this.addChild(this.spine);
        this.spine.state.data.setMix('static','win', 0.15);
        this.spine.state.data.setMix('win','static',0.15);
        this.spine.state.addListener({
            complete: (entry: TrackEntry) => {
                Tweener.addCaller(this, {
                    count: 1,
                    time: 0,
                    onComplete: () => {
                        this.playIdleAnimation(true);
                    }
                });
            }
        })

        this.playIdleAnimation(true)
    }
    private playIdleAnimation(isLoop:boolean){
        this.spine.state.setEmptyAnimations(0);
        this.spine.state.setAnimation(0, 'static', isLoop);
    }
    public playWinAnimation():void{
        this.spine.state.setAnimation(0,'win',false);
    }
}