import {Container, DisplayObject, Sprite} from 'pixi.js';
import SoundList from '../../ignition-interactive/common/sound/SoundList';
import AssetsManager from '../../ignition-interactive/core/assets/AssetsManager';
import Sound from '../../ignition-interactive/core/sound/Sound';
import SoundManager from '../../ignition-interactive/core/sound/SoundManager';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import IAdjustableLayout from '../../ignition-interactive/core/view/IAdjustableLayout';
import { ScreenOrientation } from '../../ignition-interactive/core/view/ScreenOrientation';
import { UpdateLayoutDescription } from '../../ignition-interactive/core/view/UpdateLayoutDescription';
import SoundListExtended from '../sound/SoundListExtended';
import MainGameScreen from './MainGameScreen';
import {Spine} from '@pixi-spine/all-4.1'
export default class MainScreenBackground extends Container implements IAdjustableLayout {
    private static DEFAULT_BACKGROUND_CHANNEL: string = 'ambient';

    private _theme: BackgroundType;
    private backgroundMusic: Sound;

    private currentAnimation: Spine;
    private currentSprite: Sprite


    // VISUALS
    private mainAnimation: Spine;
    private mainGameScreen: MainGameScreen;
    private freegameAnimation: Spine;

    private transitionAnimation:Spine;
    private staticBackground: Sprite;
    constructor(mainGameScreen:MainGameScreen) {
        super();
        this.mainGameScreen = mainGameScreen;


        const mainAnimationAsset = AssetsManager.spine.get(BackgroundType.NORMAL);
        const freegameAnimationAsset = AssetsManager.spine.get(BackgroundType.FREEGAME);
        this.mainAnimation = new Spine(mainAnimationAsset);
        this.freegameAnimation= new Spine(freegameAnimationAsset);

        const transitionAnimationAsset = AssetsManager.spine.get('transition');
        this.transitionAnimation = new Spine(transitionAnimationAsset);
        this.transitionAnimation.pivot.set(this.transitionAnimation.width/2, this.transitionAnimation.height/2);
        this.updateBackgroundMusic();
    }
    public getTheme(): BackgroundType {
        return this._theme;
    }
    public async setTheme(type: BackgroundType):Promise<void> {
        if (this._theme === type) {
            return;
        }

        this._theme = type;
        switch (this._theme) {
            case BackgroundType.NORMAL:
                await this.swap(BackgroundType.NORMAL, this.mainAnimation);
                break;
            case BackgroundType.FREEGAME:
                SoundManager.play(SoundList.TRANSITION);
                await this.swap(BackgroundType.FREEGAME, this.freegameAnimation);
                break;
            case BackgroundType.BONUS:
                SoundManager.play(SoundList.TRANSITION);
                await this.swap(BackgroundType.BONUS,this.freegameAnimation);
                break;
        }
    }

    private swap(theme:BackgroundType, background:Spine): Promise<void> {

        const t = theme;

        return new Promise(resolve => {
            if(this.children.length == 0) {
                this.addChild(background);
                this.addChild(this.transitionAnimation);
                background.state.setAnimation(0, theme == BackgroundType.NORMAL?'basegame':'freegame', true);
                this.mainGameScreen.changeMainUI(t);
                resolve();
                return
            }
            const prevBackground: DisplayObject = this.children[0];

            // Adjust the time for transition animation to cover the UI changes
            const transitionCoverTimeOut:number = 500;

            this.mainGameScreen.addChild(this.transitionAnimation);

            this.transitionAnimation.state.setEmptyAnimations(0);

            this.transitionAnimation.state.addListener({
                start:()=>{
                    setTimeout(() => {
                        this.removeChild(prevBackground);
                        this.addChildAt(background,0)

                        this.mainGameScreen.changeMainUI(t);

                    }, transitionCoverTimeOut); },

                complete: () => {
                    resolve();
                    this.transitionAnimation.state.clearListeners();

                }
            })
            this.transitionAnimation.state.setAnimation(0, 'transition', false);
            background.state.setAnimation(0, theme == BackgroundType.NORMAL?'basegame':'freegame', true);


            if (this.backgroundMusic) {
                Tweener.addTween(this.backgroundMusic,
                    {
                        volume: 0,
                        time: 0.45,
                        transition: 'linear',
                        onComplete: () => {
                            this.backgroundMusic.stop();
                            this.updateBackgroundMusic()
                        }
                    });
            }
        })
    }

    private updateBackgroundMusic(): void {
        this.backgroundMusic?.stop();
        this.backgroundMusic = SoundManager.loop({
            id: this._theme === BackgroundType.NORMAL ? SoundListExtended.BASEGAME_BACKGROUND : SoundListExtended.FREEGAME_BACKGROUND,
            volume: this._theme === BackgroundType.NORMAL ? 0.1 : 0.2,
            channel: MainScreenBackground.DEFAULT_BACKGROUND_CHANNEL
        });
    }

    public updateLayout(desc: UpdateLayoutDescription) {
        switch (desc.orientation) {
            case ScreenOrientation.HORIZONTAL:
                const xScale: number = desc.currentWidth / desc.baseWidth;
                const yScale: number = desc.currentHeight / desc.baseHeight;
                this.transitionAnimation.scale.set(xScale > yScale ? xScale : yScale);
                this.scale.set(xScale > yScale ? xScale : yScale);
                break;
            case ScreenOrientation.VERTICAL:
                // const offsetX = (desc.currentWidth - desc.baseWidth) / 2
                // const backgroundWidth = 1920
                // this.scale.set((desc.currentHeight / desc.baseHeight) * 2);
                // this.pivot.x = -(backgroundWidth / 2)
                // this.x = 0 - offsetX
                this.transitionAnimation.scale.set((desc.currentHeight / desc.baseHeight) * 2);
                this.scale.set((desc.currentHeight / desc.baseHeight) * 2);
                // Keep center after rescaling
                this.pivot.x = (desc.baseWidth / desc.currentWidth);
                // Ordinary offset
                this.x = 120
                break;
        }
        
        this.transitionAnimation.position.set(this.x, this.y);
    }
}

export enum BackgroundType {
    NORMAL = 'main-screen-background',
    FREEGAME = 'freegame-screen-background',
    BONUS = 'bonus-screen'
}
