import {Container, Point} from 'pixi.js';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import LayoutBuilder from '../../ignition-interactive/core/utils/LayoutBuilder';
import MultiplierCell from './MultiplierCell';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import {container} from 'tsyringe';
import {RoundResult} from '../../ignition-interactive/slots/model/RoundResult';
import { HoldAndSpinnerRoundResult } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';

export default class FreespinMultiplierBoard extends Container{
    private currentValueOn:number ;

    private multiplierCellsArr:MultiplierCell[] = [];

    private bonusx2:MultiplierCell;
    private bonusx3:MultiplierCell;
    private bonusx10:MultiplierCell;

    private bonusArr:MultiplierCell[] = [];
    constructor(le:LayoutElement) {
        super()
        LayoutBuilder.create(le, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });
        for (let i:number = 0; this['multiplier'+i] ; i++) {
            const m:MultiplierCell = this['multiplier'+i];
            this.multiplierCellsArr.push(m);
        }
        this.multiplierCellsArr.splice(3, 0, this.bonusx2);
        this.multiplierCellsArr.splice(7, 0, this.bonusx3);
        this.multiplierCellsArr.push(this.bonusx10);
        this.currentValueOn = 0;

        this.deactivateCells();

    }
    public restore():void{
        this.currentValueOn = (container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.collectedCount??0;
        if(this.currentValueOn > 12){
            this.currentValueOn = 12
        }

        if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.toCollect && this.currentValueOn != 0)
            this.currentValueOn--

        for (let i:number = 0; i < this.currentValueOn ; i++) {
            this.multiplierCellsArr[i].activate(false);
        }
    }
    public async update():Promise<void> {
        if(this.currentValueOn == 12){
            return
        }
        this.currentValueOn +=1;
        await this.multiplierCellsArr[this.currentValueOn-1].activate();
        this.updateBonuses()
    }
    public getNextCell():MultiplierCell | null {

        // multiplier should move to each cell position
        if (this.currentValueOn < 0 || this.currentValueOn >= this.multiplierCellsArr.length) {
            console.warn(`FreespinMultiplierBoard.getNextCell: currentValueOn (${this.currentValueOn}) is out of bounds for multiplierCellsArr (length: ${this.multiplierCellsArr.length})`);
            return null;
        }
        return this.multiplierCellsArr[this.currentValueOn];
    }
    public deactivateCells(){
        this.multiplierCellsArr.forEach((cell:MultiplierCell) => {
            cell.deactivate()
            }
        );
        this.bonusArr.forEach((cell:MultiplierCell) => {
            cell.deactivate()
        })
        this.currentValueOn = 0;
    }

    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        switch (le.customClass){
            case 'MultiplierCell':
                instance = new MultiplierCell(le);
            break
        }

        return instance;
    }


    private updateBonuses():void {
        const sm:SlotMachine = container.resolve(SlotMachine);
        if((sm.roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier == 1){
           this.bonusArr.forEach((bonus:MultiplierCell)=>{
               bonus.deactivate();
           })
        }
        else if(  (sm.roundResult as  RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier == 2){
            this.bonusx2.activate();
        }
        else if(  (sm.roundResult as  RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier == 3){
            this.bonusx3.activate();
        }
        else if(  (sm.roundResult as  RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier == 10){
            this.bonusx10.activate();
        }

    }
}