    constructor(baseUrl: string, historyUrl:string,jwtToken: string, gameCode: string, userLoginUrl: string = '', creditUrl:string = '') {
        super();
        this.jwtToken = jwtToken;
        this.gameCode = gameCode;
        this.userLoginUrl = userLoginUrl;
        this.ax = axios.create({
            baseURL: baseUrl,
            headers: {
                'Authorization': `Bearer ${this.jwtToken}`
            }
        });

        this.ax.interceptors.request.use(async config => {
            if (config.url != 'Game/KeepAlive') {
                await this.checkAndUpdateToken();
            }
            return config;
        }, error => {
            Promise.reject(error)
        });


        this.hx = axios.create({
            baseURL: historyUrl,
            headers: {
                'Authorization': `Bearer ${this.jwtToken}`
            }
        });

        this._settings = getFromLocalStorage('settings') ??{
            ambientMusic: true,
            soundFx: true,
        };
        this._autospinSettings = {
            skipScreens:false,
            limitlessAutoSpin:false,
            turboSpin:false,
            sliderAmount:0
        }
    }

