    private pickOneConverter(output:number[][], pickOneSymbols: number[]):void{
        for (let i = 0; i < output.length; i++) {
            for (let j = 0; j < output[i].length; j++) {
                if(pickOneSymbols.includes(output[i][j])) {
                    output[i][j] = this.removeFirstDigit(output[i][j]);
                }
            }
        }
    }

    private removeFirstDigit(num: number): number {
        const numStr: string = num.toString();
        if (numStr.length >= 2) {
            const newNumStr: string  = numStr.slice(1);
            return parseInt(newNumStr);
        }
    }

    private parseLinePattern(data: LoginResponse): number[][] {
        return Object.entries(data.GameInfo.Patterns)
            .sort((a, b) => parseInt(a[0]) - parseInt(b[0]))
            .map(entry => entry[1].map(e => Math.floor(e / entry[1].length)));
    }

    private parseSymbolMultiplier(data: LoginResponse): RuleDescription[] {
        const symbolMap = {
            HP4: 204,
            HP3: 203,
            HP2: 202,
            HP1: 201,
            LP5: 105,
            LP4: 104,
            LP3: 103,
            LP2: 102,
            LP1: 101,
            FS: 25,
            HS: 301
        }

        return Object.entries(data.GameInfo.SymbolMultiplier)
            .flatMap(entry => Object.entries(entry[1]).flatMap(inner => ({
                id: 0,
                pattern: {
                    symbolId: symbolMap[entry[0]],
                    symbolCount: [parseInt(inner[0])],
                    type: PatternType.BOTH_WAYS
                },
                reward: {
                    line: {
                        multiplier: inner[1]
                    }
                }
            })));
    }
