import { Slot, Spine, TrackEntry } from '@pixi-spine/all-4.1'
import { Container, Text } from 'pixi.js';
import AssetsManager from '../../../core/assets/AssetsManager';
import Sound from '../../../core/sound/Sound';
import SoundManager from '../../../core/sound/SoundManager';
import { Tweener } from '../../../core/tweener/ignitionTween';
import { SpinePopupAnimationConfig } from './SpinePopupAnimationConfig';

export default class SpinePopup extends Container {

    // array for all files in current type of popup
    private spine:Spine;
    private loopedSounds:Sound[] = [];
    private config: SpinePopupAnimationConfig;

    constructor(config: SpinePopupAnimationConfig) {
        super();

        this.config = config;

        // get all the assets from config
        const asset = AssetsManager.spine.get(this.config.assetName);

        // add spines to array
        this.spine = new Spine(asset); 

        this.on('added', this.onAdded, this);
        this.on('removed', this.onRemoved, this);
    }

    private onAdded():void {
        this.addChild(this.spine);

        // get the animations and duration
        const { start, loop, end } = this.config.animationsSelector();
        const loopAnimationCount = this.config.loopAnimationCount ?? 4;

        // duration of value counting up
        const countUpDuration: number = 4;
        const appearTime:number = 0.3
        const disappearTime:number = 0.5

        this.spine.state.setEmptyAnimations(0);
        this.config.slotOverrides.forEach(override => {
            const text: Text = override.textProvider();
            text.anchor.set(0.5);
            const index = this.spine.skeleton.findSlotIndex(override.name ?? 'placeholder');
            const slot: Slot = this.spine.skeleton.slots[index];
        
            if(index !== -1) {
                text.scale.y = -text.scale.y;
                const counterContainer: Container = this.spine.slotContainers[index];
                counterContainer.removeChildren();
                counterContainer.addChild(text);
            }
            else {
                this.addChildAt(text, this.children.length);
            }

            const renderer = override.valueRenderer ?? ((value: number) => value.toString());
            if(override.type === 'animated') {
                this.spine.state.addListener({
                    start: (entry: TrackEntry) => {
                        // on start animation make slot or only Text appear
                        if (entry.animation.name == start) {
                            if(slot) {
                                slot.color.a = 0;
                                Tweener.addTween(slot.color, {
                                    a: 1,
                                    time: appearTime,
                                    transition: 'easeOutQuad',
                                    delay: appearTime,
                                });
        
                                slot.bone.scaleX = slot.bone.scaleY = 0.1;
                                Tweener.addTween(slot.bone, {
                                    scaleX: 1,
                                    scaleY: 1,
                                    time: appearTime,
                                    transition: 'easeOutBack',
                                    delay:appearTime,
                                });
                            } else if(text) {
                                text.alpha = 0 ;
                                Tweener.addTween(text, {
                                    alpha: 1,
                                    time:appearTime,
                                    transition: 'easeOutQuad',
                                })
                            }
        
                        // on end animation make slot or Text disappear
                        } else if (entry.animation.name == end) {
                            if(slot) {
                                Tweener.addTween(slot.color, {
                                    a: 0,
                                    time: disappearTime,
                                    transition: 'easeOutQuad',
                                })
                            }
                            else if(text){
                                Tweener.addTween(text, {
                                    alpha: 0,
                                    time:disappearTime,
                                    transition: 'easeOutQuad',
        
                                })
                            }
                        }
                    }
                });

                // do count up animation of win value
                text['__SpinePopupValue__'] = 0;
                text.text = renderer(text['__SpinePopupValue__']);
                
                Tweener.addTween(text, {
                    __SpinePopupValue__: override.valueProvider(),
                    time: countUpDuration,
                    transition: 'easeInOutQuad',
                    onUpdate: () => {
                        text.text = renderer(text['__SpinePopupValue__']);
                    },
                    onStart: () => {
                        if(override.soundLoopProvider) {
                            this.loopedSounds.push(SoundManager.loop(override.soundLoopProvider()));
                        }
                    },
                    onComplete: () => {
                        this.loopedSounds?.forEach(sound => sound.stop());
                        if(override.soundEndProvider) {
                            SoundManager.play(override.soundEndProvider());
                        }
                    },
                })
            } else {
                // if there is no count up then just set the win to provided value
                text.text = renderer(override.valueProvider());
            }
        });

        // play animations in correct order
        this.spine.state.data.setMix(start, loop, 0.7);
        this.spine.state.setAnimation(0, start, false);
        for (let i:number = 0; i < loopAnimationCount; i++) {
            this.spine.state.addAnimation(0, loop, false, 0);
        }
        this.spine.state.addAnimation(0, end, false, 0);
    }

    private onRemoved():void{
        this.loopedSounds.forEach(sound => sound.stop());
        this.loopedSounds.length = 0;

        Tweener.removeTweens(this);

        this.config.slotOverrides.forEach(override => {
            const slot: Slot = this.spine.skeleton.findSlot(override.name ?? 'placeholder')
            if(slot){
                Tweener.removeTweens(slot.bone);
                Tweener.removeTweens(slot.color);
                slot.setToSetupPose();
            }
        });

        this.spine.state.setEmptyAnimations(0);
        this.spine.state.clearListeners();
        this.spine['lastTime'] = null;
    }
}