import { Container } from 'pixi.js';
import { container } from 'tsyringe';
import AssetsManager from '../../core/assets/AssetsManager';
import { UpdateLayoutDescription } from '../../core/view/UpdateLayoutDescription';
import SlotMachine from '../../slots/model/SlotMachine';
import PopupConnectionLost from '../view/popup/PopupConnectionLost';
import PopupFeatureBuy from '../view/popup/PopupFeatureBuy';
import PopupNotEnoughBalance from '../view/popup/PopupNotEnoughBalance';
import AdjustBetPanel from '../view/ui/AdjustBetPanel';
import AutospinPanel from '../view/ui/AutospinPanel';
import PaytablePanelDesktop from '../view/ui/PaytablePanelDesktop';
import PaytablePanelMobile, { PaytablePanelConfig } from '../view/ui/PaytablePanelMobile';

import { FeatureTypes } from '../model/FeatureTypes';
import { PopupFactoryTokenConstants } from '../tsyringe/PopupFactoryTokenConstants';
import PopupCongratulations from '../view/popup/PopupCongratulations';
import PopupFreespins from '../view/popup/PopupFreespins';
import PopupHoldAndSpinner from '../view/popup/PopupHoldAndSpinner';
import PopupTurboActivate from '../view/popup/PopupTurboActivate';
import SpinePopup from '../view/popup/SpinePopup';
import { SpinePopupAnimationConfig } from '../view/popup/SpinePopupAnimationConfig';
import SystemSettingsPanel from '../view/ui/SystemSettingsPanel';

export enum PopupFactoryType {
    BIG_WIN_HORIZONTAL = 'bigWinHorizontal',
    BIG_WIN_FREESPIN_HORIZONTAL = 'bigWinFreeSpinHorizontal',
    BIG_WIN_FREESPIN_VERTICAL = 'bigWinFreeSpinVertical',
    BIG_WIN_VERTICAL = 'bigWinVertical',
    HISTORY_VERTICAL = 'historyVertical',
    HISTORY_HORIZONTAL = 'historyHorizontal',
    AUTOSPIN_SETTINGS_HORIZONTAL = 'autospinSettingsHorizontal',
    AUTOSPIN_SETTINGS_VERTICAL = 'autospinSettingsVertical',
    ADJUST_BET_HORIZONTAL = 'adjustBetHorizontal',
    ADJUST_BET_VERTICAL = 'adjustBetVertical',
    PAYTABLE_HORIZONTAL = 'paytableHorizontal',
    PAYTABLE_VERTICAL = 'paytableVertical',
    MENU_SETTINGS_VERTICAL = 'systemSettingsVertical',
    MENU_SETTINGS_HORIZONTAL= 'systemSettingsHorizontal',
    BALANCE_HORIZONTAL = 'balanceHorizontal',
    BALANCE_VERTICAL = 'balanceVertical',
    CONNECTION_LOST_HORIZONTAL = 'connectionLostHorizontal',
    CONNECTION_LOST_VERTICAL = 'connectionLostVertical',
    FREESPIN_BUY_HORIZONTAL = 'freespinBuyHorizontal',
    FREESPIN_BUY_VERTICAL = 'freespinBuyVertical',
    HOLD_AND_SPINNER_BUY_HORIZONTAL = 'holdAndSpinnerBuyHorizontal',
    HOLD_AND_SPINNER_BUY_VERTICAL = 'holdAndSpinnerBuyVertical',
    FREESPIN_POPUP_HORIZONTAL = 'freespinPopupHorizontal',
    FREESPIN_POPUP_VERTICAL = 'freespinPopupVertical',
    CONGRATULATIONS_POPUP_HORIZONTAL = 'congratulationsHorizontal',
    CONGRATULATIONS_POPUP_VERTICAL = 'congratulationsVertical',
    EXTRA_FREESPIN_POPUP_HORIZONTAL = 'extraFreespinPopupHorizontal',
    EXTRA_FREESPIN_POPUP_VERTICAL = 'extraFreespinPopupVertical',
    HOLD_SPINNER_POPUP_HORIZONTAL = 'holdSpinnerPopupHorizontal',
    HOLD_SPINNER_POPUP_VERTICAL = 'holdSpinnerPopupVertical',
    TURBO_ACTIVATE_HORIZONTAL = 'turboActivateHorizontal',
    TURBO_ACTIVATE_VERTICAL = 'turboActivateVertical'
}

export class PopupFactory {
    private static popups: Map<string, Container>;

    private static initialized: boolean = false;
    private static slotMachine: SlotMachine;
    private static popupTurboPosYHorizontal:number;
    private static popupTurboPosYVertical:number;
    private static paytableConfig:PaytablePanelConfig;

    private static popupBigWinConfig0: SpinePopupAnimationConfig;
    private static popupBigWinConfig1: SpinePopupAnimationConfig;
    private static popupBigWinConfig2: SpinePopupAnimationConfig;
    private static popupBigWinConfig3: SpinePopupAnimationConfig;

    private static popupCongratulationsConfig: SpinePopupAnimationConfig;
    private static popupExtraFreespinConfig: SpinePopupAnimationConfig;
    private static popupFreespinsConfig: SpinePopupAnimationConfig;
    private static popupHoldAndSpinnerConfig: SpinePopupAnimationConfig;

    constructor() { throw new Error(`${this.constructor.name} is a static class`) }

    private static retrieveTemplate(popupType: string, provider: () => Container, updateLayoutDescription?: UpdateLayoutDescription) {
        let popup = this.popups.get(popupType);
        if(!popup) {
            popup = provider();
            this.popups.set(popupType, popup);
        }

        if(updateLayoutDescription) {
            popup['updateLayout']?.(updateLayoutDescription);
        }

        return popup;
    }

    public static retrieveBigWinHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            `${PopupFactoryType.BIG_WIN_HORIZONTAL}${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`,
            () => new SpinePopup(
                this[`popupBigWinConfig${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`],
            ),
            updateLayoutDescription)
    }

    public static retrieveBigWinVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            `${PopupFactoryType.BIG_WIN_VERTICAL}${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`,
            () => new SpinePopup(
                this[`popupBigWinConfig${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`],
            ),
            updateLayoutDescription
        );
    }

    public static retrieveBigWinFreeSpinHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            `${PopupFactoryType.BIG_WIN_FREESPIN_HORIZONTAL}${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`,
            () => new SpinePopup(
                this[`popupBigWinConfig${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`],
            ),
            updateLayoutDescription)
    }

    public static retrieveBigWinFreeSpinVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            `${PopupFactoryType.BIG_WIN_FREESPIN_VERTICAL}${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`,
            () => new SpinePopup(
                this[`popupBigWinConfig${this.slotMachine.bigWinLevel(this.slotMachine.roundResult)}`],
            ),
            updateLayoutDescription
        );
    }

    public static retrieveCongratulationPopupHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.CONGRATULATIONS_POPUP_HORIZONTAL,
            () => new PopupCongratulations(
                this.popupCongratulationsConfig,
                () => this.slotMachine.roundResult.totalWinValue

            ),
            updateLayoutDescription)
    }

    public static retrieveCongratulationPopupVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.CONGRATULATIONS_POPUP_VERTICAL,
            () => new PopupCongratulations(
                this.popupCongratulationsConfig,
                () => this.slotMachine.roundResult.totalWinValue
            ),
            updateLayoutDescription
        );
    }
    public static retrieveExtraFreespinPopupHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.EXTRA_FREESPIN_POPUP_HORIZONTAL,
            () => new SpinePopup(this.popupExtraFreespinConfig),
            updateLayoutDescription
        );
    }

    public static retrieveExtraFreespinPopupVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.EXTRA_FREESPIN_POPUP_VERTICAL,
            () => new SpinePopup(this.popupExtraFreespinConfig),
            updateLayoutDescription
        );
    }
    public static retrieveFreespinPopupHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.FREESPIN_POPUP_HORIZONTAL,
            () => new PopupFreespins(
                this.popupFreespinsConfig,
                () => this.slotMachine.currentSpinResult.freespins.totalCount,
            ),
            updateLayoutDescription)
    }

    public static retrieveFreespinPopupVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.FREESPIN_POPUP_VERTICAL,
            () =>new PopupFreespins(
                this.popupFreespinsConfig,
                () => this.slotMachine.currentSpinResult.freespins.totalCount,
            ),
            updateLayoutDescription
        );
    }
    public static retrieveHoldAndSpinnerPopupHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.HOLD_SPINNER_POPUP_HORIZONTAL,
            () => new PopupHoldAndSpinner(this.popupHoldAndSpinnerConfig),
            updateLayoutDescription)
    }

    public static retrieveHoldAndSpinnerPopupVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.HOLD_SPINNER_POPUP_VERTICAL,
            () =>new PopupHoldAndSpinner(this.popupHoldAndSpinnerConfig),
            updateLayoutDescription
        );
    }
    public static retrieveTurboActivatePopupHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.TURBO_ACTIVATE_HORIZONTAL,
            () =>new PopupTurboActivate(this.popupTurboPosYHorizontal),
            updateLayoutDescription
        );
    }

    public static retrieveTurboActivatePopupVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.TURBO_ACTIVATE_VERTICAL,
            () =>new PopupTurboActivate(this.popupTurboPosYVertical),
            updateLayoutDescription
        );
    }

    public static retrieveAutospinSettingsHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.AUTOSPIN_SETTINGS_HORIZONTAL,
            () => new AutospinPanel(
                AssetsManager.layouts.get('AutospinPanelDesktop'),
            ),
            updateLayoutDescription
        )
    }

    public static retrieveAutospinSettingsVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.AUTOSPIN_SETTINGS_VERTICAL,
            () => new AutospinPanel(
                AssetsManager.layouts.get('AutospinPanelMobile'),

            ),
            updateLayoutDescription
        )
    }

    public static retrieveAdjustBetHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.ADJUST_BET_HORIZONTAL,
            () => new AdjustBetPanel(
                AssetsManager.layouts.get('AdjustBetPanel'),
            ),
            updateLayoutDescription
        )
    }

    public static retrieveAdjustBetVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.ADJUST_BET_VERTICAL,
            () => new AdjustBetPanel(
                AssetsManager.layouts.get('AdjustBetPanelMobile')
            ),
            updateLayoutDescription
        )
    }
    public static retrieveSystemSettingsHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.MENU_SETTINGS_HORIZONTAL,
            () => new SystemSettingsPanel(AssetsManager.layouts.get('MenuSettingsPanel')),
            updateLayoutDescription
        )
    }
    public static retrieveSystemSettingsVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.MENU_SETTINGS_VERTICAL,
            () => new SystemSettingsPanel(AssetsManager.layouts.get('MenuSettingsPanelMobile')),
            updateLayoutDescription
        )
    }

    public static retrievePaytableHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.PAYTABLE_HORIZONTAL,
            () => new PaytablePanelDesktop(this.paytableConfig),
            updateLayoutDescription
        )
    }

    public static retrievePaytableVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.PAYTABLE_VERTICAL,
            () => new PaytablePanelMobile(this.paytableConfig),
            updateLayoutDescription
        )
    }

    public static retrieveBalanceHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.BALANCE_HORIZONTAL,
            () => new PopupNotEnoughBalance(),
            updateLayoutDescription
        )
    }

    public static retrieveBalanceVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.BALANCE_VERTICAL,
            () => new PopupNotEnoughBalance(),
            updateLayoutDescription
        )
    }

    public static retrieveConnectionLostHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.CONNECTION_LOST_HORIZONTAL,
            () => new PopupConnectionLost(),
            updateLayoutDescription
        )
    }

    public static retrieveConnectionLostVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.CONNECTION_LOST_VERTICAL,
            () => new PopupConnectionLost(),
            updateLayoutDescription
        )
    }
    public static retrieveFreespinBuyHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.FREESPIN_BUY_HORIZONTAL,
            () => new PopupFeatureBuy(FeatureTypes.FREESPIN),
            updateLayoutDescription
        )
    }
    public static retrieveFreespinBuyVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.FREESPIN_BUY_VERTICAL,
            () => new PopupFeatureBuy(FeatureTypes.FREESPIN),
            updateLayoutDescription
        )
    }
    public static retrieveHoldAndSpinnerBuyHorizontal(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.HOLD_AND_SPINNER_BUY_HORIZONTAL,
            () => new PopupFeatureBuy(FeatureTypes.HOLD_AND_SPINNER),
            updateLayoutDescription
        )
    }
    public static retrieveHoldAndSpinnerBuyVertical(updateLayoutDescription?: UpdateLayoutDescription) {
        return this.retrieveTemplate(
            PopupFactoryType.HOLD_AND_SPINNER_BUY_VERTICAL,
            () => new PopupFeatureBuy(FeatureTypes.HOLD_AND_SPINNER),
            updateLayoutDescription
        )
    }

    public static retrieve(popupType: PopupFactoryType, updateLayoutDescription?: UpdateLayoutDescription) {
        switch(popupType) {
            case PopupFactoryType.BIG_WIN_HORIZONTAL:
                return PopupFactory.retrieveBigWinHorizontal(updateLayoutDescription);
            case PopupFactoryType.BIG_WIN_VERTICAL:
                return PopupFactory.retrieveBigWinVertical(updateLayoutDescription);
            case PopupFactoryType.BIG_WIN_FREESPIN_HORIZONTAL:
                return PopupFactory.retrieveBigWinFreeSpinHorizontal(updateLayoutDescription);
            case PopupFactoryType.BIG_WIN_FREESPIN_VERTICAL:
                return PopupFactory.retrieveBigWinFreeSpinVertical(updateLayoutDescription);
            case PopupFactoryType.HISTORY_HORIZONTAL:
            case PopupFactoryType.HISTORY_VERTICAL:
            case PopupFactoryType.AUTOSPIN_SETTINGS_HORIZONTAL:
                return PopupFactory.retrieveAutospinSettingsHorizontal(updateLayoutDescription);
            case PopupFactoryType.AUTOSPIN_SETTINGS_VERTICAL:
                return PopupFactory.retrieveAutospinSettingsVertical(updateLayoutDescription);
            case PopupFactoryType.ADJUST_BET_HORIZONTAL:
                return PopupFactory.retrieveAdjustBetHorizontal(updateLayoutDescription);
            case PopupFactoryType.ADJUST_BET_VERTICAL:
                return PopupFactory.retrieveAdjustBetVertical(updateLayoutDescription);
            case PopupFactoryType.PAYTABLE_HORIZONTAL:
                return PopupFactory.retrievePaytableHorizontal(updateLayoutDescription);
            case PopupFactoryType.PAYTABLE_VERTICAL:
                return PopupFactory.retrievePaytableVertical(updateLayoutDescription);
            case PopupFactoryType.MENU_SETTINGS_VERTICAL:
                return PopupFactory.retrieveSystemSettingsVertical(updateLayoutDescription);
            case PopupFactoryType.MENU_SETTINGS_HORIZONTAL:
                return PopupFactory.retrieveSystemSettingsHorizontal(updateLayoutDescription);
            case PopupFactoryType.BALANCE_HORIZONTAL:
                return PopupFactory.retrieveBalanceHorizontal(updateLayoutDescription);
            case PopupFactoryType.BALANCE_VERTICAL:
                return PopupFactory.retrieveBalanceVertical(updateLayoutDescription);
            case PopupFactoryType.CONNECTION_LOST_HORIZONTAL:
                return PopupFactory.retrieveConnectionLostHorizontal(updateLayoutDescription);
            case PopupFactoryType.CONNECTION_LOST_VERTICAL:
                return PopupFactory.retrieveConnectionLostVertical(updateLayoutDescription);
            case PopupFactoryType.FREESPIN_BUY_HORIZONTAL:
                return PopupFactory.retrieveFreespinBuyHorizontal(updateLayoutDescription);
            case PopupFactoryType.FREESPIN_BUY_VERTICAL:
                return PopupFactory.retrieveFreespinBuyVertical(updateLayoutDescription);
            case PopupFactoryType.HOLD_AND_SPINNER_BUY_HORIZONTAL:
                return PopupFactory.retrieveHoldAndSpinnerBuyHorizontal(updateLayoutDescription);
            case PopupFactoryType.HOLD_AND_SPINNER_BUY_VERTICAL:
                return PopupFactory.retrieveHoldAndSpinnerBuyVertical(updateLayoutDescription);
            case PopupFactoryType.FREESPIN_POPUP_HORIZONTAL:
                return PopupFactory.retrieveFreespinPopupHorizontal(updateLayoutDescription);
            case PopupFactoryType.FREESPIN_POPUP_VERTICAL:
                return PopupFactory.retrieveFreespinPopupVertical(updateLayoutDescription);
            case PopupFactoryType.EXTRA_FREESPIN_POPUP_HORIZONTAL:
                return PopupFactory.retrieveExtraFreespinPopupHorizontal(updateLayoutDescription);
            case PopupFactoryType.EXTRA_FREESPIN_POPUP_VERTICAL:
                return PopupFactory.retrieveExtraFreespinPopupVertical(updateLayoutDescription);
            case PopupFactoryType.CONGRATULATIONS_POPUP_HORIZONTAL:
                return PopupFactory.retrieveCongratulationPopupHorizontal(updateLayoutDescription);
            case PopupFactoryType.CONGRATULATIONS_POPUP_VERTICAL:
                return PopupFactory.retrieveCongratulationPopupVertical(updateLayoutDescription);
            case PopupFactoryType.HOLD_SPINNER_POPUP_HORIZONTAL:
                return PopupFactory.retrieveHoldAndSpinnerPopupHorizontal(updateLayoutDescription);
            case PopupFactoryType.HOLD_SPINNER_POPUP_VERTICAL:
                return PopupFactory.retrieveHoldAndSpinnerPopupVertical(updateLayoutDescription);
            case PopupFactoryType.TURBO_ACTIVATE_HORIZONTAL:
                return PopupFactory.retrieveTurboActivatePopupHorizontal(updateLayoutDescription);
            case PopupFactoryType.TURBO_ACTIVATE_VERTICAL:
                return PopupFactory.retrieveTurboActivatePopupVertical(updateLayoutDescription);
            default:
                throw new Error(`${this.constructor.name} does not know how to handle the '${popupType}'`);
        }
    }

    public static initialize() {
        if(this.initialized) {
            throw new Error(`${this.constructor.name} has been already initialized`);
        }

        this.popups = new Map();
        this.popupTurboPosYVertical = container.resolve(PopupFactoryTokenConstants.POPUP_TURBO_POSITION_Y_VERTICAL);
        this.popupTurboPosYHorizontal = container.resolve(PopupFactoryTokenConstants.POPUP_TURBO_POSITION_Y_HORIZONTAL);
        this.slotMachine = container.resolve(SlotMachine);
        this.paytableConfig = container.resolve(PopupFactoryTokenConstants.PAYTABLE_CONFIG);

        this.popupBigWinConfig0 = container.resolve(PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_0);
        this.popupBigWinConfig1 = container.resolve(PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_1);
        this.popupBigWinConfig2 = container.resolve(PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_2);
        this.popupBigWinConfig3 = container.resolve(PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_3);

        this.popupFreespinsConfig = container.resolve(PopupFactoryTokenConstants.POPUP_FREESPINS_CONFIG);
        this.popupHoldAndSpinnerConfig = container.resolve(PopupFactoryTokenConstants.POPUP_HOLD_AND_SPINNER_CONFIG);
        this.popupCongratulationsConfig = container.resolve(PopupFactoryTokenConstants.POPUP_CONGRATULATIONS_CONFIG);
        this.popupExtraFreespinConfig = container.resolve(PopupFactoryTokenConstants.POPUP_EXTRA_FREESPIN_CONFIG);
    }
}