import { Container } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import AssetsManager from '../../../core/assets/AssetsManager';
import ControlEvent from '../../../core/control/event/ControlEvent';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Button from '../../../core/view/ui/Button';
import { container } from 'tsyringe';
import SlotMachine from '../../../slots/model/SlotMachine';
import { SlotMachineState } from '../../../slots/model/SlotMachineState';

export default class PopupNotEnoughBalance extends Container {
    constructor() {
        super();
        LayoutBuilder.create(AssetsManager.layouts.get('PopupNotEnoughBalance'), this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });
        this.interactive = true;
        // Add event listener for click interaction
        this.on('pointerdown', this.onPopupClick,this);
    }

    protected customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            case 'Button':
                instance = new Button(le);
                break;
        }
        return instance;
    }


    private onPopupClick(): void {
        // Dispatch the HIDE_POPUP event when clicked
        new ControlEvent(UIPanelEvent.HIDE_POPUP).dispatch();
        const sm: SlotMachine = container.resolve(SlotMachine);
        sm.currentState = SlotMachineState.IDLE;
    }

    public destroy(): void {
        // Cleanup: Remove the event listener
        this.off('pointerdown', this.onPopupClick);
        super.destroy();
    }
}
