import EventEmitter from 'eventemitter3';
import { container } from 'tsyringe';
import SlotMachine from '../../slots/model/SlotMachine';
import { AdjustBetSettingsStateEvent } from './event/AdjustBetSettingsStateEvent';

export default class AdjustBetSettingsState extends EventEmitter {
    private _currentBetId: number = 0;
    private _currentBetValue: number;
    
    public updateCurrentBetId(value: number) {
        const sm: SlotMachine = container.resolve(SlotMachine);
        const betLimits: number[] = sm.description.betLimits;
        this._currentBetId = Math.min(Math.max(0, value), betLimits.length - 1);
        this._currentBetValue = betLimits[this._currentBetId];

        this.emit(AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, this._currentBetId);
        this.emit(AdjustBetSettingsStateEvent.CURRENT_BET_VALUE_UPDATE, this._currentBetValue);
    }

    public get currentBetId(): number {
        return this._currentBetId;
    }

    public get curretnBetValue(): number {
        return this._currentBetValue;
    }
}