import {BitmapText, Container, DisplayObject, Graphics, Sprite, Text} from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';


export default class TextSpriteCombined extends Container{
    private area:Graphics
    private content:Container;

    private tfAbove:Text|BitmapText;
    private tfUnder:Text|BitmapText;
    constructor(le,spacing:number = 20) {
        super();
        LayoutBuilder.create(le, this);
        if(this.area)
            this.area.alpha = 0;
        this.repositionChildren(spacing)
    }

    private repositionChildren(spacing:number):void {
        const objects:DisplayObject[] = this.content.children
        for (let i:number = 0; i < objects.length ; i++) {
            if(i == 0){
                objects[0].x = 0
            }
            else{
                (objects[i] as Text|Sprite).x = (objects[i-1] as Text|Sprite).x +  (objects[i-1] as Text|Sprite).width + spacing
            }
            if(this.tfUnder){
                if(this.tfUnder instanceof Text){
                    this.tfUnder.style.align = 'center'
                }
                this.tfUnder.y = this.content.y + this.content.height;
                this.tfUnder.pivot.y = 0;
            }
            if(this.tfAbove){
                if(this.tfAbove instanceof Text){
                    this.tfAbove.style.align = 'center'
                }
                this.tfAbove.y = this.content.y - this.tfAbove.height;
                this.pivot.y = this.tfAbove.y
            }
        }
        this.content.pivot.x = this.content.width/2;

        if(this.area)
            this.content.x = this.area.width/2;
    }

}