import { Spine } from '@pixi-spine/all-4.1'
import { Container, Text, TextStyle } from 'pixi.js';
import FeatureWinBanner from '../../ignition-interactive/common/view/features-components/FeatureWinBanner';
import SoundManager from '../../ignition-interactive/core/sound/SoundManager';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import LayoutBuilder from '../../ignition-interactive/core/utils/LayoutBuilder';
import IAdjustableLayout from '../../ignition-interactive/core/view/IAdjustableLayout';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import { UpdateLayoutDescription } from '../../ignition-interactive/core/view/UpdateLayoutDescription';
import ReelsView from '../../ignition-interactive/slots/view/ReelsView';
import SymbolView from '../../ignition-interactive/slots/view/SymbolView';
import SoundListExtended from '../sound/SoundListExtended';
import { IAnimationStrategy } from '../strategies/animation/IAnimationStrategy';
import { FreespinMultiplierAnimationStrategy } from '../strategies/animation/MultiplierAnimationStrategy';
import FreespinMultiplierBoard from './FreespinMultiplierBoard';

export default class FeatureView extends Container implements IAdjustableLayout{
    protected reels:ReelsView;
    protected  banner:FeatureWinBanner|FreespinMultiplierBoard;

    protected _animationMoveStrategy: IAnimationStrategy<Text> = new FreespinMultiplierAnimationStrategy();

    public layoutDesc: UpdateLayoutDescription;

    constructor(le:LayoutElement, reels:ReelsView, banner:FeatureWinBanner|FreespinMultiplierBoard) {
        super();
        LayoutBuilder.create(le,this);

        this.reels = reels;
        this.banner = banner;

    }
    updateLayout(desc: UpdateLayoutDescription): void {
        this.layoutDesc = desc;
    }

    protected animateSymbol(symbolView:SymbolView,animationName:string, delay:number=0):void {
        setTimeout(()=> {
            symbolView.spineAnim.visible = true;
            symbolView.spineAnim.state.setAnimation(0, animationName, false);
            SoundManager.play(SoundListExtended.MULTIPLIER)

        },delay * 1000)
    }

    protected createMultiplier(symbol:SymbolView,value:string,style:TextStyle): Text {
        const m:Text = new Text( value, style);
        m.pivot.set(m.width/2, 0);
        symbol.addChild(m);
        this.animateScale(m);
        m.name = "multiplier";

        return m;
    }
    private animateScale(m:Text):void {
        m.scale.set(0);
        Tweener.addTween(m.scale, {
            x:1.1,
            y:1.1,
            time:0.5,
            onComplete: ()=>{
                Tweener.addTween(m.scale, {
                    x:1,
                    y:1,
                    time:0.4
                })
            }
        })
    }
    protected resetSpine(spine:Spine){
        spine.visible = false;
        spine.state.setEmptyAnimations(0);
        spine.state.clearListeners();
        spine.skeleton.setToSetupPose()
        spine['lastTime'] = null;
    }

}