import EventEmitter from 'eventemitter3';
import { container } from 'tsyringe';
import SlotMachine from '../../slots/model/SlotMachine';
import { SpeedSettingsStateEvent } from './event/SpeedSettingsStateEvent';

export default class SpeedSettingsState extends EventEmitter {
    private _currentSpeedLevel: number = 0;

    constructor() {
        super();
        const sm: SlotMachine = container.resolve(SlotMachine);
        this._currentSpeedLevel = sm.currentGameSpeedLevel;
    }

    public updateGameSpeedLevel(value: number) {
        const sm: SlotMachine = container.resolve(SlotMachine);
        this._currentSpeedLevel = Math.max(Math.min(sm.options.gameSpeedLevels.length, value), 0);

        this.emit(SpeedSettingsStateEvent.CURRENT_SPEED_LEVEL_SET, this._currentSpeedLevel);
    }

    public get currentSpeedLevel(): number {
        return this._currentSpeedLevel;
    }
}