import { Container, Sprite } from 'pixi.js';
import { container } from 'tsyringe';
import { Tweener } from '../../../core/tweener/ignitionTween';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import LayoutElement from '../../../core/view/model/LayoutElement';
import { ScreenOrientation } from '../../../core/view/ScreenOrientation';
import ValueText from '../../../core/view/text/ValueText';
import Wallet from '../../../slots/model/Wallet';
import MultiShadowText from '../text/MultiShadowText';
import TextFit from '../TextFit';

export default class FeatureWinBanner extends Container {

    private bcgMobile:Sprite;
    private bcgDesktop:Sprite;

    private winValue: TextFit|ValueText|MultiShadowText;


    constructor(le:LayoutElement) {
        super();
        LayoutBuilder.create(le, this, (le:LayoutElement) => {
            return this.customClassElementCreate(le);
        })
        if(this.winValue instanceof ValueText) {
            this.winValue.renderValueFunction = (tf, value) => {
                tf.text = `${container.resolve(Wallet).getCurrencyValue(value, true)}`;
            };
        }

    }
    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        switch (le.customClass) {
            case 'ValueText':
                instance = new ValueText(le);
                break;
            case 'MultiShadowText':
                instance = new MultiShadowText(le);
                break;
            case "TextFit":
                instance = new TextFit(le);
                break;
        }

        return instance;
    }

    public setValue(value: number) {
        if(this.winValue instanceof ValueText) {
            this.winValue.value = Number(value.toFixed(2));
        }
        else if (this.winValue instanceof TextFit) {
            this.winValue.setText(`${container.resolve(Wallet).getCurrencyValue(value, true)}`);
        } else {
            this.winValue.text =`${container.resolve(Wallet).getCurrencyValue(value, true)}`
        }
            
       this.winAnimation()
        if (value > 0) {
            this.winValue.alpha = 1;
        }
        else {
            this.reset();
        }
    }


    private reset(): void {
        if(this.winValue instanceof ValueText) {
            this.winValue.value = 0;
        }
        else if(this.winValue instanceof TextFit) {
            this.winValue.setText(`${container.resolve(Wallet).getCurrencyValue(0, true)}`);
        }
        else {
            this.winValue.text = `${container.resolve(Wallet).getCurrencyValue(0, true)}`;
        }
        this.winValue.alpha = 0;
    }

    public updateBackground(orientation:ScreenOrientation){
        if(orientation == ScreenOrientation.VERTICAL){
            this.bcgMobile.visible = true;
            this.bcgDesktop.visible = false;
        }
        else {
            this.bcgMobile.visible = false;
            this.bcgDesktop.visible = true;
        }
    }
    private winAnimation(): void {


        const totalWinIn: number = 0.6;
        const totalWinOut: number = 0.5;

        Tweener.addTween(this.winValue.scale, {
            x: 1.5,
            y: 1.5,
            time: totalWinIn,
            transition: 'easeOutQuint',
            onComplete: () => {
                Tweener.addTween(this.winValue.scale, {
                    x: 1,
                    y: 1,
                    time: totalWinOut,
                    transition: 'easeInQuint',
                });
            }
        });

    }

}