import {Container, Text, TextStyle, Texture} from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import {autoscaleText} from '../../../core/utils/TextUtils';

export default class MultiShadowText extends Container{
    private _textStyle:TextStyle;

    private tfArray:Text[] = [];
    constructor(le, shadowDistance:number = 3, shadowAngle:number = 2.5) {
        super();
        LayoutBuilder.create(le, this);
        for (let i:number = 0; this['tf'+i] ; i++) {
            this.tfArray.push(this['tf'+i]);
        }
        this._textStyle = this['tf0'].style;

        this.tfArray.forEach((tf,index) =>{
            tf.position.x = tf.position.x + Math.cos(shadowAngle) * shadowDistance * index;
            tf.position.y = tf.position.y +  Math.sin(shadowAngle) * shadowDistance * index;
        })



    }
    public get text():Text {
        return this.tfArray[0]
    }
    public set text(text: string) {
        this.tfArray.forEach(tf=>{
            tf.text = text
        })
    }
    public get style(){
        return this._textStyle
    }
    public set style(style:TextStyle) {
        this._textStyle = style;

        this.tfArray.forEach(tf=>{
            console.log(tf)
            tf.style = style
        })
    }

    public autoScale(fontSize, width, height) {
        this.tfArray.forEach((tf) => autoscaleText(tf, fontSize, width, height))
    }
}