import { Slot, Spine, TrackEntry } from '@pixi-spine/all-4.1'
import { BitmapText, Container, Text } from 'pixi.js';
import { container } from 'tsyringe';
import AssetsManager from '../../../core/assets/AssetsManager';
import Sound from '../../../core/sound/Sound';
import SoundManager from '../../../core/sound/SoundManager';
import { Tweener } from '../../../core/tweener/ignitionTween';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Wallet from '../../../slots/model/Wallet';
import SoundList from '../../sound/SoundList';
import { SpinePopupAnimationConfig } from './SpinePopupAnimationConfig';

export default class PopupCongratulations extends Container{
    private animation:Spine;

    private winValue: number | (() => number);
    private _value: number;

    public tfAmount: Text | BitmapText;

    private loopedSound: Sound;

    constructor(private config: SpinePopupAnimationConfig, winValue:number | (()=>number)) {
        super();
        const layout = AssetsManager.layouts.has('PopupCelebration')
            ? AssetsManager.layouts.get('PopupCelebration')
            : AssetsManager.layouts.get('PopupFreespins');

        LayoutBuilder.create(layout, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        if (this.config.scale)
            this.animation.scale.set(this.config.scale);

        this.winValue = winValue;
        this.on('added', this.onAdded, this);
        this.on('removed', this.onRemoved, this);
        this.winValue = (winValue instanceof Function ? winValue : () => winValue)

    }

    // PRIVATE API
    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        if(le.customClass.endsWith('Animation')) {
            const asset = AssetsManager.spine.get(this.config.assetName);
            instance = new Spine(asset);
        }

        return instance;
    }
    // PUBLIC API
    public get value(): number {
        return this._value;
    }

    public set value(value: number) {
        this._value = value;

        const wallet: Wallet = container.resolve(Wallet);
        this.tfAmount.text = wallet.getCurrencyValue(value, true);
    }

    private onAdded(): void {
        this.removeChild(this.animation);
        this.addChild(this.animation);
        const index = this.animation.skeleton.findSlotIndex('placeholder');
        const slot = this.animation.skeleton.slots[index];
        const counterContainer: Container = this.animation.slotContainers[index];
        counterContainer.removeChildren();
        counterContainer.addChild(this.tfAmount);
        slot.bone.scaleX = 0.3;
        slot.bone.scaleY = -0.3;
        const countUpDuration: number = 4;
        const { start, loop, end } = this.config.animationsSelector();
        this.animation.state.addListener({
            start: (entry: TrackEntry) => {
                if (entry.animation.name == start) {
                    slot.color.a = 0;
                    Tweener.addTween(slot.color, {
                        a: 1,
                        time: 0.3,
                        transition: 'easeOutQuad',
                        delay: 0.3,
                    });
                    Tweener.addTween(slot.bone, {
                        scaleX: 1,
                        scaleY: -1,
                        time: countUpDuration / 8,
                        transition: 'easeOutBack',
                        delay: 0.3,
                    });
                } else if (entry.animation.name == end) {
                    Tweener.addTween(slot.color, {
                        a: 0,
                        time: 0.5,
                        transition: 'easeInQuad',
                    });
                }
            },
        });

        this.value = 0;
        Tweener.addTween(this, {
            value: this.winValue instanceof Function ? this.winValue() : this.winValue,
            time: countUpDuration,
            transition: 'easeInOutQuad',
            onStart: () => {
                this.loopedSound = SoundManager.loop({
                    id: SoundList.COUNTER_LOOP,
                    volume: 0.25,
                });
            },
            onComplete: () => {
                this.loopedSound.stop();
                SoundManager.play({
                    id: SoundList.COUNTER_END,
                    volume: 0.4,
                });
            },
        });


        const numLoopAnimations = 5;
        this.animation.state.data.setMix(start, loop, 0.7);
        this.animation.state.setAnimation(0, start, false);
        for (let i = 0; i < numLoopAnimations; i++) {
            this.animation.state.addAnimation(0, loop, true, 0);
        }
        this.animation.state.addAnimation(0, end, false, 0);
    }
    private onRemoved(): void {
        if (this.loopedSound) this.loopedSound.stop();

        Tweener.removeTweens(this);
        this.animation.state.setEmptyAnimations(0);
        this.animation.state.clearListeners();
        this.animation['lastTime'] = null;

    }
}