import { DisplayObject, Point, Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import { HoldAndSpinnerRoundResult, MultiplierMap, SymbolsBaseMultiplierMap } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import HoldAndSpinnerStaticSymbolMap from '../../ignition-interactive/common/model/HoldAndSpinnerStaticSymbolMap';
import { CommonTokenConstants } from '../../ignition-interactive/common/tsyringe/tokens/CommonTokenConstants';
import FeatureWinBanner from '../../ignition-interactive/common/view/features-components/FeatureWinBanner';
import TotalWinFrame from '../../ignition-interactive/common/view/TotalWinFrame';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import { ScreenOrientation } from '../../ignition-interactive/core/view/ScreenOrientation';
import { UpdateLayoutDescription } from '../../ignition-interactive/core/view/UpdateLayoutDescription';
import { RoundResult } from '../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import Wallet from '../../ignition-interactive/slots/model/Wallet';
import ReelsView from '../../ignition-interactive/slots/view/ReelsView';
import ReelView from '../../ignition-interactive/slots/view/ReelView';
import { SymbolData } from '../../ignition-interactive/slots/view/SymbolData';
import SymbolView from '../../ignition-interactive/slots/view/SymbolView';
import { MultiplierEvent } from '../model/MultiplierEvent';
import { Transformation } from '../model/Transformation';
import FeatureView from './FeatureView';
import { SymbolsList } from './SymbolsList';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import { autoscaleText } from '../../ignition-interactive/core/utils/TextUtils';

export default class HoldAndSpinnerView extends FeatureView{
    public currentMultiplierSummary:number;

    private _mobileBanner:TotalWinFrame;

    private finalPos;

    constructor(le:LayoutElement, reels:ReelsView, winBanner:FeatureWinBanner) {
        super(le,reels,winBanner);
    }
    public set mobileBanner(banner:TotalWinFrame) {
        this._mobileBanner = banner;
    }
    public updateLayout(desc: UpdateLayoutDescription) {
        super.updateLayout(desc);

        if(desc.orientation == ScreenOrientation.VERTICAL)
            this.finalPos = this._mobileBanner.totalWinValue;

        else
            this.finalPos = this.banner;

    }

    private restore(initializationRestoredMap, staticSymbolData):void{
        initializationRestoredMap.forEach((item:MultiplierMap):void=>{
            const newSymbol:SymbolView = new SymbolView(staticSymbolData)
            container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.push({x:item.x, y:item.y, symbol:newSymbol})
            this.reattachSymbol(newSymbol,item.x, item.y,item.multiplier);
        })
    }

    private reattachSymbol(newSymbol:SymbolView, reelIndex:number, symbolIndex:number, multiplier:number = null){
        const reels:ReelView[] = this.reels.getReelViews();

        const oldChild:DisplayObject = reels[reelIndex].getMainStripe().children[symbolIndex];

        // create multiplier for new reattached symbol
        if(multiplier)
            this.createMultiplier(newSymbol,container.resolve(Wallet).getCurrencyValue((multiplier * container.resolve(SlotMachine).totalBet)),container.resolve(CommonTokenConstants.MULTIPLIER_STYLE))
        else
            this.recreateMultiplierFromSymbol(oldChild,newSymbol);

        const position:Point = this.toLocal(oldChild.getGlobalPosition());
        this.addChild(newSymbol)
        newSymbol.position = position;
        oldChild.visible = false;
    }

    public initializeHoldAndSpinnerView(staticSymbolId:number, movingSymbolId:number){
        const reels:ReelView[] = this.reels.getReelViews();

        if(container.resolve(SlotMachine).currentSpinResult.currentTotalWinValue > 0)
            this.currentMultiplierSummary = container.resolve(SlotMachine).currentSpinResult.currentTotalWinValue;
        else
            this.currentMultiplierSummary = 0;

        // get symbol data for each symbol ID
        const movingSymbolData:SymbolData = SymbolsList.find(s => s.id === movingSymbolId);
        const staticSymbolData:SymbolData = SymbolsList.find(s => s.id === staticSymbolId);

        const initializationRestoredMap:MultiplierMap[] = (container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap?.moneySymbolMultiplierMap.initialFixedMap;
        if(initializationRestoredMap){

            // if the initialization map is not empty restore static symbols with information from this map
            this.restore(initializationRestoredMap,staticSymbolData)

            // all the rest of the symbols transform into moving symbols assets
            this.updateMovingSymbols(staticSymbolId,movingSymbolData);
        }
        else {
            (this.banner as FeatureWinBanner)['reset']();
            container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap = [];

            // all the rest of the symbols transform into moving symbols assets
            this.updateMovingSymbols(staticSymbolId,movingSymbolData);

            // pick every hold&spinner symbol from reel and place it in THIS view
            reels.forEach((reel:ReelView, reelIndex:number):void => {
                reel.getVisibleSymbols().forEach((symbol:SymbolView, symbolIndex:number):void => {

                    if(symbol.data.id == staticSymbolId){
                        const newSymbol:SymbolView = new SymbolView(staticSymbolData);
                        this.reattachSymbol(newSymbol,reelIndex,symbolIndex);

                        // push symbol as starting symbol
                        container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.push({x:reelIndex,y:symbolIndex, symbol:newSymbol});
                    }
                })
            })
        }


    }
    private updateMovingSymbols(staticSymbolId,movingSymbolData){
        const reels:ReelView[] = this.reels.getReelViews();
        reels.forEach((reel:ReelView, reelIndex:number):void => {
            reel.getVisibleSymbols().forEach((symbol:SymbolView, symbolIndex:number) => {

                // if the symbol has diff id then static symbol -> change it to moving symbol asset
                if(symbol.data.id !== staticSymbolId){
                    this.reels.getReelViews()[reelIndex].swapSymbol(symbolIndex, new SymbolView(movingSymbolData));
                }

            })
        })
    }
    public async collectNewMultipliers(multiplierMap:SymbolsBaseMultiplierMap):Promise<void> {
        const newSymbolsArr:SymbolView[]=[];

        multiplierMap.moneySymbolMultiplierMap.newWinningsMap.forEach((symbol: MultiplierMap): void => {
            const isSymbolInInitializedMap:boolean = container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.some(initSymbol =>
                // check if the multipliers map has new symbol ( diff x and y than starting symbols )
                symbol.x === initSymbol.x && symbol.y === initSymbol.y
            );

            // if there is new symbol get it's SymbolView and push it to array for animate
            if (!isSymbolInInitializedMap) {
                const newSymbolView: SymbolView = this.reels.getReelViews()[symbol.x].getVisibleSymbols()[symbol.y];
                newSymbolsArr.push(newSymbolView);
            }
        });

        // if there aren't any new symbols return
        if(!newSymbolsArr.length)
            return;

        // move all symbols' ( starting and new ones ) multipliers
        await this.moveNewMultipliers(newSymbolsArr);

        await this.collectInitializedMultipliers();

    }

    private async moveNewMultipliers(newSymbolsArr):Promise<void> {
        const moveAnimations: Promise<void>[] = [];
        newSymbolsArr.forEach((symbol:SymbolView,index):void => {
            const multiplier =symbol.children.find((object:DisplayObject) => object.name == 'multiplier')

            if(multiplier){
                moveAnimations.push(
                    this.moveMultiplier(multiplier as Text, this.finalPos.getGlobalPosition(), index *0.8, MultiplierEvent.ON_MULTIPLIER_ANIMATION_END, symbol )
                )
            }

        })

        await Promise.all(moveAnimations);
    }

    public async collectInitializedMultipliers():Promise<void>{
        const moveAnimations: Promise<void>[] = [];

        //get multipliers from each starting symbol
        container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.forEach((map,index)=>{
            const multiplier = map.symbol.children.find((object:DisplayObject) => object.name == 'multiplier')
            if(multiplier){
                moveAnimations.push(
                    this.moveMultiplier(multiplier as Text, this.finalPos.getGlobalPosition(), index *0.8, MultiplierEvent.ON_MULTIPLIER_ANIMATION_END, map.symbol)
                );
            }
        })

        await Promise.all(moveAnimations).then(() =>{
            this.emit(MultiplierEvent.ON_ALL_MULTIPLIER_ANIMATION_END);
        });

    }

    public async moveMultiplier(multiplier: Text, point: Point, delay: number = 0, eventName: MultiplierEvent, symbol:SymbolView): Promise<void> {
        // reattach multiplier from symbol
        const multiplierOldPos: Point = multiplier.getGlobalPosition();
        multiplier.setParent(this);
        multiplier.position = this.toLocal(multiplierOldPos);

        const time: number = 0.8;
        const transformation: Transformation = {
            translation: this.toLocal(point),
            scale: new Point(1.8, 1.8)
        };

        // animate winning symbol
        this.animateSymbol(symbol,'win',delay);

        await this._animationMoveStrategy.animate(multiplier,time,delay,transformation);

        this.currentMultiplierSummary += (parseFloat((multiplier as Text).text.slice(0,-1).replace('.', '').replace(',','.'))) * Wallet.denomination
        this.emit(eventName, this.currentMultiplierSummary);

        // recreate multiplier for same symbol after moving it to banner
        await this.createMultiplier(symbol,multiplier.text,multiplier.style);

        multiplier.destroy();

    }


    public clearHoldAndSpinnerView():void{
        // if there aren't any starting symbol return
        if(!container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.length)
            return;

        container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap.forEach((staticSymbol:{x:number,y:number,symbol:SymbolView}):void=>{

            // create new symbol from old symbol data
            const symbolCopy:SymbolView = new SymbolView(staticSymbol.symbol.data);

            // recreate multiplier from old symbol
            this.recreateMultiplierFromSymbol(staticSymbol.symbol, symbolCopy);

            // reattach symbol from this view to reel
            this.reels.getReelViews()[staticSymbol.x].swapSymbol(staticSymbol.y,symbolCopy);
            staticSymbol.symbol.destroy();
        })

        // clear starting symbols array
        container.resolve(HoldAndSpinnerStaticSymbolMap).initializedSymbolsMap = [];
        (this.banner as FeatureWinBanner)['reset']();
    }


    private recreateMultiplierFromSymbol(oldChild, newSymbol):void{

        // get multiplier from old symbol and attach it to new one
        const multiplier = oldChild.children.find((object:DisplayObject):boolean => object.name == 'multiplier')
        if(multiplier){
            const m:Text = new Text((multiplier as Text).text, (multiplier as Text).style);
            m.name = 'multiplier';
            m.pivot.set(m.width/2, 0);
            newSymbol.addChild(m);
        }
    }

    public override createMultiplier(symbol: SymbolView, value: string, style: TextStyle): Text {
        const text = super.createMultiplier(symbol, value, style);
        
        Tweener.pauseTweens(text);
        const scaleX = text.scale.x;
        const scaleY = text.scale.y;
        text.scale.set(1);
        autoscaleText(text, 60, symbol.spineAnim.width, symbol.spineAnim.height);
        text.pivot.set(text.width * 0.5, 0);
        text.scale.set(scaleX, scaleY)
        Tweener.resumeTweens(text);

        return text;
    }

}