import { Text } from 'pixi.js';
import { Tweener } from '../../../ignition-interactive/core/tweener/ignitionTween';
import {IAnimationStrategy} from './IAnimationStrategy';
import {Transformation} from '../../model/Transformation';

export class BaseMultiplierAnimationStrategy implements IAnimationStrategy<Text> {
    public async animate(target: Text, time: number, delay: number = 0, data: Transformation): Promise<void> {
        return new Promise(resolve => {
            const { translation, scale } = data;
            Tweener.addTween(target, {
                x: translation.x,
                time,
                delay,
                transition: 'linear'
            });

            Tweener.addTween(target, {
                y: translation.y,
                time,
                delay,
                transition: 'easeOutSine'
            });

            Tweener.addCaller(target, {
                count: 1,
                time,
                onComplete: () => resolve()
            });
        });
    }
}

export class FreespinMultiplierAnimationStrategy implements IAnimationStrategy<Text> {
    public async animate(target: Text, time: number, delay: number, data: Transformation, scaleDown:boolean = false): Promise<void> {
        return await new Promise(resolve => {
            const { translation, scale } = data;




            Tweener.addTween(target.scale, {
                x: scale.x,
                y: scale.y,
                time: time/4,
                delay: delay,
                transition: 'easeOut',
                onComplete: () => {
                    Tweener.addTween(target.scale, {
                        x: 1,
                        y: 1,
                        time: time/2,
                        transition: 'linear',
                        onComplete: () => {
                            Tweener.addTween(target, {
                                x: translation.x,
                                time,
                                transition: 'easeOut'
                            });
                            Tweener.addTween(target, {
                                y: translation.y,
                                time,
                                transition: 'linear',
                                onComplete: () => {
                                    if(scaleDown ) {
                                        const scaleTime :number= 0.4;
                                        time += scaleTime*2;
                                        Tweener.addTween(target.scale, {
                                            x: 1.1,
                                            y: 1.1,
                                            time:scaleTime,
                                            onComplete: () => {
                                                Tweener.addTween(target.scale, {
                                                    x:0,
                                                    y:0,
                                                    time:scaleTime,
                                                    onComplete: () => resolve()
                                                });

                                            }
                                        })
                                    } else {
                                        resolve()
                                    }
                                }
                            });
                        }
                    });


                }
            })
        });
    }
}
