import {container} from 'tsyringe';
import SlotMachine from '../../../ignition-interactive/slots/model/SlotMachine';
import {PopupFactoryTokenConstants} from '../../../ignition-interactive/common/tsyringe/PopupFactoryTokenConstants';
import SoundList from '../../../ignition-interactive/common/sound/SoundList';
import { SpinePopupAnimationConfig } from '../../../ignition-interactive/common/view/popup/SpinePopupAnimationConfig';
import Wallet from '../../../ignition-interactive/slots/model/Wallet';

export const BigWinConfiguration: SpinePopupAnimationConfig = {
    assetName: `big_win`,
    animationsSelector: () => ({
        start: 'in',
        loop: 'loop',
        end: 'out'
    }),
    slotOverrides: [
        {
            type: "animated",
            name: "counter",
            textProvider: () => container.resolve(PopupFactoryTokenConstants.CELEBRATION_WIN_TEXT_HORIZONTAL),
            valueProvider: () => {
                const slotMachine:SlotMachine = container.resolve(SlotMachine);
                return slotMachine.currentSpinResult.freespins?.remainingCount
                    ? slotMachine.roundResult.actualSpinWinValue
                    : slotMachine.roundResult.totalWinValue;
            },
            valueRenderer: (value) => container.resolve(Wallet).getCurrencyValue(value, true),
            soundLoopProvider:()=> ({
                id: SoundList.COUNTER_LOOP,
                volume: 0.25
            }),
            soundEndProvider:()=> ({
                id: SoundList.COUNTER_END,
                volume: 0.4
            }),
        }
    ],
    loopAnimationCount:10,
}

