import { Container, Graphics, Sprite, Text, TextStyle } from 'pixi.js';
import ButtonSpinAnimation from '../../ignition-interactive/common/view/ButtonSpinAnimation';
import { multiStyledText } from '../../ignition-interactive/common/view/text/TextFormat';
import AssetsManager from '../../ignition-interactive/core/assets/AssetsManager';
import SoundManager from '../../ignition-interactive/core/sound/SoundManager';
import { Tweener } from '../../ignition-interactive/core/tweener/ignitionTween';
import LayoutBuilder from '../../ignition-interactive/core/utils/LayoutBuilder';
import AdjustableLayoutContainer from '../../ignition-interactive/core/view/AdjustableLayoutContainer';
import LayoutElement from '../../ignition-interactive/core/view/model/LayoutElement';
import { ScreenOrientation } from '../../ignition-interactive/core/view/ScreenOrientation';
import Button from '../../ignition-interactive/core/view/ui/Button';
import { UpdateLayoutDescription } from '../../ignition-interactive/core/view/UpdateLayoutDescription';
import IntroScreenBackground from './IntroScreenBackground';
import { IntroScreenEvent } from '../../ignition-interactive/common/view/event/IntroScreenEvent';
import ButtonSpin from '../../ignition-interactive/common/view/ButtonSpin';
import SoundList from '../../ignition-interactive/common/sound/SoundList';

export default class IntroScreen extends AdjustableLayoutContainer {

    // VISUALS
    private logo: Sprite;
    private btnGetStarted: ButtonSpin;
    private background: IntroScreenBackground;
    private tfIntroText:Container;
    private tfIntroTextMobile:Text;

    private shadowMobile:Graphics;
    constructor() {
        super(AssetsManager.layouts.get('intro-screen'));
        LayoutBuilder.create(this.layout, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        this.tfIntroTextMobile.style.align = 'center';

        this.btnGetStarted.on('pointerup', this.onGetStartedClicked, this);

        const styleArr:Partial<TextStyle>[] =[
            {
                fontFamily: AssetsManager.webFonts.get('LuckiestGuy').family,
                fill:[
                    0xffffff
                ],

            },
            {
                fontFamily: AssetsManager.webFonts.get('LuckiestGuy').family,
                fill:[
                    0x60f6ff,
                    0x7efbff,
                    0x7ceaf5
                ],
                fillGradientType:1

            }, {
                fontFamily: AssetsManager.webFonts.get('LuckiestGuy').family,
                fill:[
                    0xffffff
                ],

            }

        ];
        multiStyledText(this.tfIntroText['text'] as Text ,styleArr, ['O', 'x'] )

        this.on('added', this.onAdded,this)
        this.on('removed', this.onRemoved, this);
    }
    public updateLayout(desc: UpdateLayoutDescription) {
        super.updateLayout(desc);


        this.tfIntroText.pivot.set(0,this.tfIntroText.height/2)
        if(desc.orientation == ScreenOrientation.VERTICAL){
            this.tfIntroTextMobile.x = desc.baseWidth/2;
            this.shadowMobile.y =desc.baseHeight + (desc.currentHeight - desc.baseHeight) / 2;
            this.shadowMobile.width = desc.currentWidth;
            this.tfIntroTextMobile.pivot.set(this.tfIntroTextMobile.width/2, this.tfIntroTextMobile.height/2)
            this.tfIntroTextMobile.position.set(this.shadowMobile.x, this.shadowMobile.y - this.shadowMobile.height/2);

        }


    }

    // PRIVATE API
    private onRemoved(): void {
        Tweener.removeTweens(this);
        this.btnGetStarted.removeTweens()
    }

    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            case 'ButtonGetStarted':
                instance = new ButtonSpin(le);
                const btn = instance as Button;
                break;
            case 'IntroScreenBackground':
                instance = new IntroScreenBackground();
                break;
        }

        return instance;
    }
    private onAdded():void{
        this.btnGetStarted.waitAnimation(ButtonSpinAnimation.WAIT_DELAY)
    }
    // USER INTERACTION
    private onGetStartedClicked(): void {
        this.btnGetStarted.enabled = false;
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        this.emit(IntroScreenEvent.ON_GET_STARTED_CLICKED);
    }
}
