import { Container, Text } from 'pixi.js';
import { container } from 'tsyringe';
import { SpinePopupAnimationConfig } from '../view/popup/SpinePopupAnimationConfig';
import { PopupFactoryTokenConstants } from './PopupFactoryTokenConstants';
import { BigWinConfiguration, GrandWinConfiguration, MegaWinConfiguration, SuperWinConfiguration } from '../../../the-miner/config/popups/PopupBigWinConfig';
import PopupFreespinsConfig from '../../../the-miner/config/popups/PopupFreespinsConfig';
import PopupCongratulationsConfig from '../../../the-miner/config/popups/PopupCongratulationsConfig';
import PopupHoldAndSpinnerConfig from '../../../the-miner/config/popups/PopupHoldAndSpinnerConfig';
import PopupExtraFreespinConfig from '../../../the-miner/config/popups/PopupExtraFreespinConfig';

container.register<Text>(
    PopupFactoryTokenConstants.CELEBRATION_WIN_TEXT_HORIZONTAL,
    { useValue: new Text() }
)
container.register<Text>(
    PopupFactoryTokenConstants.CELEBRATION_WIN_TEXT_VERTICAL,
    { useToken: PopupFactoryTokenConstants.CELEBRATION_WIN_TEXT_HORIZONTAL }
)
container.register<Container|Text>(
    PopupFactoryTokenConstants.CONGRATULATION_FEATURE_TEXT_HORIZONTAL,
    { useValue: new Container()}
)
container.register<Container|Text>(
    PopupFactoryTokenConstants.CONGRATULATION_FEATURE_TEXT_VERTICAL,
    { useToken: PopupFactoryTokenConstants.CONGRATULATION_FEATURE_TEXT_HORIZONTAL }
)
container.register<number>(
    PopupFactoryTokenConstants.POPUP_FREESPIN_DESKTOP_SCALE,
    { useValue: 0.65 }
)
container.register<Text>(
    PopupFactoryTokenConstants.EXTRA_FREESPINS_TEXT_HORIZONTAL,
    { useValue: new Text() }
)
container.register<Text>(
    PopupFactoryTokenConstants.EXTRA_FREESPINS_TEXT_VERTICAL,
    { useToken: PopupFactoryTokenConstants.EXTRA_FREESPINS_TEXT_HORIZONTAL }
)

// Popup configuration
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_0,
    { useValue: BigWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_1,
    { useValue: MegaWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_2,
    { useValue: SuperWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_BIG_WIN_CONFIG_3,
    { useValue: GrandWinConfiguration }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_FREESPINS_CONFIG,
    { useValue: PopupFreespinsConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_CONGRATULATIONS_CONFIG,
    { useValue: PopupCongratulationsConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_HOLD_AND_SPINNER_CONFIG,
    { useValue: PopupHoldAndSpinnerConfig }
)
container.register<SpinePopupAnimationConfig>(
    PopupFactoryTokenConstants.POPUP_EXTRA_FREESPIN_CONFIG,
    { useValue: PopupExtraFreespinConfig }
)