import EventEmitter from 'eventemitter3';
import { Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import ControlEvent from '../../../core/control/event/ControlEvent';
import Translation from '../../../core/translations/Translation';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Button from '../../../core/view/ui/Button';
import { SlotMachineEvent } from '../../../slots/model/event/SlotMachineEvent';
import { WalletEvent } from '../../../slots/model/event/WalletEvent';
import SlotMachine from '../../../slots/model/SlotMachine';
import Wallet from '../../../slots/model/Wallet';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';
import { PopupData, PopupType } from '../../model/PopupState';
import { GameServiceEvent } from '../../services/GameServiceEvent';
import { multiStyledText } from '../text/TextFormat';
import TextFit from '../TextFit';

import SoundManager from '../../../core/sound/SoundManager';
import { FeatureTypes } from '../../model/FeatureTypes';
import SoundList from '../../sound/SoundList';
import { CommonTokenConstants } from '../../tsyringe/tokens/CommonTokenConstants';
import GameService from '../../../../the-miner/services/GameService';

export default class HoldAndSpinnerButton extends Button{
    private tfDescription:TextFit;
    private tfValue:TextFit;
    constructor(l:LayoutElement) {
        super(l, (le) => {
            let instance: unknown = null;
            switch (le.customClass) {
                case 'TextFit':
                    instance = new TextFit(le)
                    break;
            }
            return instance;
        });

        this.tfValue = this.normal['tfValue'];
        this.tfDescription = this.normal['tfDescription'];

        const sm: SlotMachine = container.resolve(SlotMachine);
        const wallet: Wallet = container.resolve(Wallet);
        const gs: GameService = container.resolve(GameService);
        (gs as unknown as EventEmitter).on(GameServiceEvent.DOUBLE_CHANCE_CHANGED, () => {
            this.setActive(!gs.doubleUpChance);
        }, this);

        const feature = gs.featureBuyConfig.config.find( feature => feature.RoundType == FeatureTypes.HOLD_AND_SPINNER);

        sm.on(SlotMachineEvent.BET_VALUE_CHANGED, () => {
            this.setTexts(Translation.t('holdSpinner.description'), wallet.getCurrencyValue(feature.Multiplier * sm.totalBet * wallet.coinValue, true));
        }, this);

        wallet.on(WalletEvent.COIN_VALUE_CHANGED, () => {
            this.setTexts(Translation.t('holdSpinner.description'), wallet.getCurrencyValue(feature.Multiplier *  sm.totalBet * wallet.coinValue, true));
        }, this);



        this.setTexts(Translation.t('holdSpinner.description'), wallet.getCurrencyValue(feature.Multiplier * sm.totalBet * wallet.coinValue, true));

        const styleArr:Partial<TextStyle>[] = container.resolve(CommonTokenConstants.HOLD_AND_SPINNER_MULTI_STYLED_TEXT);

        multiStyledText(this.tfDescription.text as Text, styleArr,['D', '&' ], 0);

        this.on('pointerup', this.onClick, this)

    }
    private onClick(): void {
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        const data: PopupData = {
            type: PopupType.HOLD_AND_SPINNER_BUY,
            hideOnClick: false,
            duration: -1,
            callbacks: null
        }
        new ControlEvent(UIPanelEvent.SHOW_POPUP, data).dispatch();
    }

    public setTexts(title: string, value: string, isActive: boolean = true): void {
        this.tfDescription.text.text = title;
        if(this.tfValue)
            this.tfValue.text.text = value;

        this.setActive(isActive);

        this.tfValue.updateLayout();
    }

    private setActive(isActive: boolean): void {
        this.enabled = isActive;
        isActive ? this.alpha = 1 : this.alpha = 0.4

    }
}
