import { container } from 'tsyringe';
import { HoldAndSpinnerRoundResult } from '../../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import { PopupFactoryTokenConstants } from '../../../ignition-interactive/common/tsyringe/PopupFactoryTokenConstants';
import { SpinePopupAnimationConfig } from '../../../ignition-interactive/common/view/popup/SpinePopupAnimationConfig';
import { RoundResult } from '../../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../../ignition-interactive/slots/model/SlotMachine';

const name = 'extra_spin' as const;
export default  {
    assetName: 'congratulation_popup',
    animationsSelector: () => ({
        start: `${name}_in`,
        loop: `${name}_loop`,
        end: `${name}_out`
    }),
    slotOverrides: [
        {
            type: 'static',
            name: "extra_spins_count",
            valueProvider: () => {
                const sm = container.resolve(SlotMachine);
                return (sm.roundResult as  RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureMultiplier;
            },
            textProvider: () => container.resolve(PopupFactoryTokenConstants.CELEBRATION_WIN_TEXT_HORIZONTAL),
            valueRenderer: (value) => `x${value}`,
        }
    ]
} satisfies SpinePopupAnimationConfig