import { Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import { HoldAndSpinnerRoundResult } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import { CommonTokenConstants } from '../../ignition-interactive/common/tsyringe/tokens/CommonTokenConstants';
import { autoscaleText } from '../../ignition-interactive/core/utils/TextUtils';
import { RoundResult, SpinResult } from '../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import { SymbolData } from '../../ignition-interactive/slots/view/SymbolData';
import SymbolView from '../../ignition-interactive/slots/view/SymbolView';

export default class WildSymbolView extends SymbolView {

    private style:TextStyle = container.resolve(CommonTokenConstants.WILD_MULTIPLIER_STYLE);


    public summaryMultiplier:Text;
    public multiplier:Text;

    constructor(data: SymbolData) {
        super(data);

        const spinResult:SpinResult = container.resolve(SlotMachine).currentSpinResult;
        const roundResult : RoundResult & HoldAndSpinnerRoundResult = container.resolve(SlotMachine).roundResult;
        const colCount:number =  roundResult.holdAndSpinnerMultiplierMap.collectedCount

        if(spinResult.freespins && roundResult.holdAndSpinnerMultiplierMap.featureMultiplier > 1 && colCount > 4){
            let wildMultiplier:number = roundResult.holdAndSpinnerMultiplierMap.featureMultiplier;

            // we are getting from api that after collecting 4 or 8 or 12 wilds the multiplier is changing x2 -> x3 ->x10
            // but if we don't change it to the previous featureMultiplier there will be higher wildMultiplier before even winning extra freespins
            // if (colCount == 8 ){
            //     wildMultiplier = 2;
            // }
            // else if (colCount == 12 ){
            //     wildMultiplier = 3;
            // }



            this.multiplier = new Text(`x${wildMultiplier}`, this.style)
            this.addChild(this.multiplier);
            autoscaleText(this.multiplier, 50, this.spineAnim.width, this.spineAnim.height);
            this.multiplier.pivot.set(this.multiplier.width/2, this.multiplier.height/2);
            this.multiplier.position.set(50, -50);

        }
    }
}