import { Container, Graphics } from 'pixi.js';
import { container } from 'tsyringe';
import { Tweener } from '../../../core/tweener/ignitionTween';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import LayoutElement from '../../../core/view/model/LayoutElement';
import { RoundResult } from '../../../slots/model/RoundResult';
import SlotMachine from '../../../slots/model/SlotMachine';
import { HoldAndSpinnerRoundResult } from '../../model/HoldAndSpinnerRoundResult';

export default class HeartBanner extends Container{
    private heart0:Heart;
    private heart1:Heart;
    private heart2:Heart;
    private heart3:Heart;

    private heartArr:Heart[]=[];

    public prevHeartLevel:number;
    private healthLevel:number;
    constructor(le:LayoutElement) {
        super();
        LayoutBuilder.create(le,this);
        for (let i:number = 0; this['heart'+i] ; i++) {
            this.heartArr.push(this['heart'+i]);
        }
        this.heartArr.reverse();
        this.restore(true);

    }
    private updateHearts(){
        this.heartArr.forEach((heart, index) => {
            if(index <= this.healthLevel-1){
                heart.active.visible = true;
                heart.disabled.visible = false;
            }
            else {
                heart.active.visible = false;
                heart.disabled.visible =true;
            }
        })
    }

    public restore(updateHearts:boolean):void{
        const sm :SlotMachine=  container.resolve(SlotMachine);
        this.prevHeartLevel = this.healthLevel??4;
        this.healthLevel =   (sm.roundResult as  RoundResult & HoldAndSpinnerRoundResult)?.holdAndSpinner?.remainingCount??4;
        if(updateHearts)
            this.updateHearts()
    }

    public async resetHearts(animate:boolean):Promise<void>{
        return new Promise(resolve => {
            if(this.healthLevel == 4){
                resolve();
                return;
            }

            this.restore(false);
            if(!animate){
                this.heartArr.forEach((element:Heart) => {
                    element.active.visible = true;
                    element.disabled.visible = false;
                })
                this.prevHeartLevel = this.healthLevel;
                this.healthLevel = 4;
                resolve();
                return
            }
            else {
                this.prevHeartLevel = this.healthLevel;
                this.healthLevel =4;
                this.animateReset(resolve);
            }
        })

    }
    private async animateReset(resolve:() =>void):Promise<void>{
        const time:number = 0.2;
        let delayIteration:number = 0;
        for (let i:number =0; i <= this.healthLevel-1; i++) {
            if (this.heartArr[i].active.visible == false) {
                Tweener.removeTweens(this.heartArr[i].active);
                Tweener.addTween(this.heartArr[i].active, {
                    onStart: () => {
                        this.heartArr[i].active.alpha = 0;
                        this.heartArr[i].active.visible = true;
                        this.heartArr[i].disabled.visible = false;
                    },
                    delay: (time*2) * delayIteration * 1.1,
                    alpha: 1,
                    time: time,
                })
                Tweener.addTween(this.heartArr[i].active.scale, {
                    x:1.5,
                    y:1.5,
                    time: time,
                    delay: (time*2) * delayIteration * 1.1,
                    onComplete: () => {
                        Tweener.addTween(this.heartArr[i].active.scale, {
                            x:1,
                            y:1,
                            time: time,
                            onComplete:()=>{
                                if(i==this.healthLevel-1) {
                                    resolve()
                                }
                            }
                        })
                    }
                })
                delayIteration++;
            }
        }
    }
    public reduceHeart():void{
        this.restore(false);
        const currHeart:Heart=this.heartArr[this.healthLevel-1];
        if(currHeart.active.visible){
            currHeart.active.visible = false;
            currHeart.disabled.visible = true;
            return
        }
    }
}
export type Heart ={
    active:Graphics;
    disabled:Graphics;
}