import { Container, Sprite, Text } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import { SymbolData } from '../../../slots/view/SymbolData';
import { container } from 'tsyringe';
import SlotMachine from '../../../slots/model/SlotMachine';
import Wallet from '../../../slots/model/Wallet';
import { SlotMachineEvent } from '../../../slots/model/event/SlotMachineEvent';
import { WalletEvent } from '../../../slots/model/event/WalletEvent';
import LayoutElement from '../../../core/view/model/LayoutElement';
import {CommonTokenConstants} from '../../tsyringe/tokens/CommonTokenConstants';
import {DekstopMobileConfiguration, PlainPoint} from '../../tsyringe/tokens/Types';
import {ScreenOrientation} from '../../../core/view/ScreenOrientation';

type PaytableSymbolCellConfig = {
    maxFontSize: number,
    maxWidth: number,
    maxHeight: number,
    setMultipliers: boolean,
    multiplierList?:number[]
}

export default class PaytableSymbolCell extends Container {

    private staticIcon: Container;
    private staticIcon2: Container;
    private symbolId:number;

    public standardSize:number;

    private multiplierDescription: Container;
    private slotMachine:SlotMachine;
    private wallet:Wallet;
    private layoutOrientation: LayoutOrientation;

    private tfPays6:Text;

    private tfPaysArr:{
        tfPays:Text,
        count:number;
    }[] = [];


    private config: PaytableSymbolCellConfig = {
        maxFontSize: 20,
        maxHeight: 60,
        maxWidth: 120,
        setMultipliers: true,
        multiplierList:[5,4,3,2]
    };

    private multiplierDescriptionSpacingY:number;
    constructor(le: LayoutElement,
                orientation:ScreenOrientation,
                symbolData: SymbolData,
                config: PaytableSymbolCellConfig = null,
                symbolDoubleData:SymbolData = null,
                standardSize:number = 0,
                iconsSpacing:number = 0,
    ) {
        super();

        LayoutBuilder.create(le, this);

        if(config)
            this.config = config;
        this.symbolId = symbolData.id;
        this.wallet = container.resolve(Wallet)
        this.slotMachine = container.resolve(SlotMachine)

        if (this.config.setMultipliers) {
            this.slotMachine.on(SlotMachineEvent.BET_VALUE_CHANGED, () => {
                this.setMultipliers(this.symbolId)
            }, this);
            this.wallet.on(WalletEvent.COIN_VALUE_CHANGED, () => {
                this.setMultipliers(this.symbolId)
            }, this);
        }

        this.multiplierDescriptionSpacingY =orientation==ScreenOrientation.HORIZONTAL?
            container.resolve<DekstopMobileConfiguration<number>>(CommonTokenConstants.PAYTABLE_PANEL_SYMBOL_DESCRIPTION_SPACING).desktop:
            container.resolve<DekstopMobileConfiguration<number>>(CommonTokenConstants.PAYTABLE_PANEL_SYMBOL_DESCRIPTION_SPACING).mobile;

        this.addIcon(symbolData, symbolDoubleData,standardSize,iconsSpacing);
        if (this.config.setMultipliers) {
            this.config.multiplierList.forEach(item=> {
                this.tfPaysArr.push({
                        tfPays: this['multiplierDescription'][`tfPays${item}`],
                        count: item
                    }
                );
            })
            this.setMultipliers(symbolData.id)
        }
        else
            this.multiplierDescription.visible = false;
    }

    private addIcon(symbolData: SymbolData, symbolDoubleData?:SymbolData,standardSize?:number ,iconSpacing:number = 0) {
        const staticIcon: Sprite = new Sprite(symbolData.staticIcon.texture);
        let staticDoubleIcon:Sprite;
        // Sprite can take null for parameter so -> new Sprite(symbol?.static.texture) <- will not work
        if(symbolDoubleData)
             staticDoubleIcon= new Sprite(symbolDoubleData.staticIcon.texture);
        staticIcon.pivot.set(staticIcon.width/2,staticIcon.height/2);

        this.staticIcon.addChild(staticIcon);
        if(staticDoubleIcon){
            staticDoubleIcon.pivot.set(staticDoubleIcon.width/2,staticDoubleIcon.height/2);
            this.staticIcon2.addChild(staticDoubleIcon);
        }
        if(!standardSize)
            standardSize = staticIcon.width;

        // change the standardized size if the starling scale is changed
        standardSize = standardSize * this.staticIcon.scale.x;
        // !!! all symbols have different texture sizes
        this.staticIcon2.x += iconSpacing + standardSize;
        this.pivot.x -= standardSize/2 - iconSpacing/2;
        this.pivot.y -= standardSize/2 - iconSpacing/2;
        this.standardSize = symbolDoubleData ? standardSize * 2 + iconSpacing : standardSize + iconSpacing ;
    }

    public get width(){
        return this.standardSize;
    }
    public get height(){
        return this.standardSize + this.multiplierDescription.height;
    }

    private setMultipliers(symbolId: number): void {
        const sm: SlotMachine = container.resolve(SlotMachine);
        if (symbolId > 100 && symbolId < 1000) {
            this.tfPaysArr.forEach((tfPays)=>{
                tfPays.tfPays.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, tfPays.count).reward.line.multiplier * container.resolve(SlotMachine).totalBet * container.resolve(Wallet).coinValue)}`
            })
        }
        this.repositionMultipliers()
    }
    private repositionMultipliers(): void {
        const spacingX:number = 36;
        const md= this['multiplierDescription'];
        this.config.multiplierList.forEach((item:number, index:number) => {
            const height:number = (md[`${item}x`] as Text).height;
            (md[`${item}x`] as Text).x = 0;
            (md[`${item}x`] as Text).y =  height * index;


            (md[`separator${item}`] as Text).x =  spacingX;
            (md[`separator${item}`] as Text).y = height * index;

            (md[`tfPays${item}`] as Text).x = (md[`separator${item}`] as Text).x + (md[`separator${item}`] as Text).width + spacingX/2;
            (md[`tfPays${item}`] as Text).y =  height * index;
        })

        this.centerMultiplierContainer()
    }
    private centerMultiplierContainer():void {

        this.multiplierDescription.pivot.set(this.multiplierDescription.width/2, 0);
        this.multiplierDescription.x = 0;
        this.multiplierDescription.y = this.standardSize/2 + this.multiplierDescriptionSpacingY;
    }
    public scatterMultiplier(symbolId, parent) {
        const sm: SlotMachine = container.resolve(SlotMachine);

        // this.tfPays6 = parent['multiplierDescription']['tfPays6'];
        // this.tfPays5 = parent['multiplierDescription']['tfPays5'];
        // this.tfPays4 = parent['multiplierDescription']['tfPays4'];
        //
        // if (symbolId == 25) {
        //     this.tfPays6.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 6).reward.line.multiplier * container.resolve(SlotMachine).totalBet * container.resolve(Wallet).coinValue)}`;
        //     this.tfPays5.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 5).reward.line.multiplier * container.resolve(SlotMachine).totalBet * container.resolve(Wallet).coinValue)}`;
        //     this.tfPays4.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 4).reward.line.multiplier * container.resolve(SlotMachine).totalBet * container.resolve(Wallet).coinValue)}`;

            //this.updateView(250, parent['multiplierDescription'].children as Text[]);
        }


}

enum LayoutOrientation {
    HORIZONTAL = 'horizontal',
    VERTICAL = 'vertical'
}