import { Container, Graphics, Text } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import LayoutElement from '../../../core/view/model/LayoutElement';

import EventEmitter from 'eventemitter3';
import { container } from 'tsyringe';
import ControlEvent from '../../../core/control/event/ControlEvent';
import SoundManager from '../../../core/sound/SoundManager';
import ValueText from '../../../core/view/text/ValueText';
import Button, { ButtonState } from '../../../core/view/ui/Button';
import { SlotMachineEvent } from '../../../slots/model/event/SlotMachineEvent';
import { WalletEvent } from '../../../slots/model/event/WalletEvent';
import SlotMachine from '../../../slots/model/SlotMachine';
import Wallet from '../../../slots/model/Wallet';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';
import AdjustBetSettingsState from '../../model/AdjustBetSettingsState';
import { AdjustBetSettingsStateEvent } from '../../model/event/AdjustBetSettingsStateEvent';
import { UIPanelType } from '../../model/UIState';
import { GameServiceEvent } from '../../services/GameServiceEvent';
import SoundList from '../../sound/SoundList';
import { CommonTokenConstants } from '../../tsyringe/tokens/CommonTokenConstants';
import GameService from '../../../../the-miner/services/GameService';

export default class AdjustTotalBet extends  Container{
    public tfTotalBetValue:ValueText
    public btnBetDown:Button
    public btnBetUp:Button
    public tfTotalBet:Text


    private slotMachine:SlotMachine;
    private wallet:Wallet
    private area:Graphics;

    public updateBet: boolean = false;
    private _currentBetID:number;
    private _currentBetValue:number;

    private settingsState: AdjustBetSettingsState;
    private gameService: GameService & EventEmitter;

    constructor(le:LayoutElement) {
        super();

        LayoutBuilder.create(le,this,(le:LayoutElement)=>{
            return this.customClassElementCreate(le)
        })
        if(this.area){
            this.area.interactive = true;
            this.area.eventMode = 'static';
            this.area.cursor = 'pointer';
            this.area.alpha = 0;
            this.area.on('pointerup', this.onClick, this)
        }

        this.wallet = container.resolve(Wallet);
        this.slotMachine = container.resolve(SlotMachine);
        this.settingsState = container.resolve(AdjustBetSettingsState);
        this.gameService = container.resolve(GameService);

        this.settingsState.on(AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, (value) => {
            this._currentBetID = value;
            this._currentBetValue = this.slotMachine.description.betLimits[this._currentBetID];
        });


        this.slotMachine.on(SlotMachineEvent.BET_VALUE_CHANGED, this.onBetValueChanged, this);
        this.wallet.on(WalletEvent.COIN_VALUE_CHANGED, this.onBetValueChanged, this);
        this.gameService.on(GameServiceEvent.DOUBLE_CHANCE_CHANGED, this.onDoubleChanceChanged, this);

        this.onBetValueChanged()


        this.btnBetDown.on('pointerup',this.onTotalBetDown, this)
        this.btnBetUp.on('pointerup',this.onTotalBetUp, this)

        this.setupButtonOnClickSound();
    }
    public get currentBetValue():number {
        return this._currentBetValue;
    }

    public customClassElementCreate(le:LayoutElement){
        let instance: unknown = null;
        switch (le.customClass) {
            case 'Button':
                instance = new Button(le)
                break
            case 'ValueText':
                instance = new ValueText(le)
                break
        }


        return instance;
    }
    public onDoubleChanceChanged(): void {
        this.updateValues();
    }
    public onBetValueChanged(): void {
        new ControlEvent(
            AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, 
            this.slotMachine.description.betLimits.indexOf(this.slotMachine.currentBetValue)
        ).dispatch();
        this.updateView();
    }
    public onAdded(){
        new ControlEvent(
            AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, 
            this.slotMachine.description.betLimits.indexOf(this.slotMachine.currentBetValue)
        ).dispatch();
        this.updateView();
    }

    private onTotalBetUp():void{
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        if(this.updateBet) {
            new ControlEvent(AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, this._currentBetID + 1).dispatch();
            this.updateView();
        }
    }
    private onTotalBetDown():void{
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        if(this.updateBet) {
            new ControlEvent(AdjustBetSettingsStateEvent.CURRENT_BET_ID_UPDATE, this._currentBetID - 1).dispatch();
            this.updateView();
        }
    }

    private updateView(): void {
        const totalBetUpActive:boolean = this._currentBetValue < this.slotMachine.description.betLimits[this.slotMachine.description.betLimits.length - 1] * Wallet.denomination
        const totalBetDownActive:boolean = this._currentBetValue > this.slotMachine.description.betLimits[0]  * Wallet.denomination;

        const view: Map<Button, boolean> = new Map<Button, boolean>([
            [this.btnBetUp, totalBetUpActive],
            [this.btnBetDown, totalBetDownActive],
        ]);

        view.forEach((active: boolean, btn: Button): void => {
            btn.setState(active ? ButtonState.NORMAL : ButtonState.DISABLED);
            btn.enabled = active;
        });

        this.updateValues();
    }

    private updateValues() {
        const coinRate: number = (<boolean>this.gameService["_doubleChanceEnabled"] ? <number>container.resolve(CommonTokenConstants.DOUBLE_CHANCE_BUTTON_COST_RATE) : 1);
        this.tfTotalBetValue.tfValue.text = `${this.wallet.getCurrencyValue(this._currentBetValue * this.slotMachine.numLines * this.wallet.coinValue * coinRate)}`;
    }

    private setupButtonOnClickSound() {
        for(const key in this) {
            const button = this[key];
            if(button instanceof Button) {
                button.on('pointerup', () => SoundManager.play(SoundList.UI_BUTTON_CLICK), this);
            }
        }
    }

    private onClick(){
        SoundManager.play(SoundList.UI_BUTTON_CLICK);
        new ControlEvent(UIPanelEvent.OPEN_PANEL, UIPanelType.BET_SETTINGS).dispatch();
    }

    public lock() {
        this.btnBetDown.enabled = false;
        this.btnBetUp.enabled = false;
        this.area.eventMode = 'auto';
        this.area.cursor = 'auto';
    }

    public unlock() {
        this.btnBetDown.enabled = true;
        this.btnBetUp.enabled = true;
        this.area.eventMode = 'dynamic';
        this.area.cursor = 'pointer';
    }
}