import { Container, Graphics, Text } from 'pixi.js';
import { container } from 'tsyringe';
import AssetsManager from '../../../core/assets/AssetsManager';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import SlotMachine from '../../../slots/model/SlotMachine';

import Translation from '../../../core/translations/Translation';
import { autoscaleText } from '../../../core/utils/TextUtils';
import { SlotMachineEvent } from '../../../slots/model/event/SlotMachineEvent';
import { PopupAnimationConfig } from '../../../slots/view/popup/PopupAnimationConfig';

export default class PopupTurboActivate extends Container {
    private normal:Graphics;
    private turbo:Graphics;
    private superTurbo:Graphics;

    private title:Text;

    public static turboPopupAnimationConfiguration: PopupAnimationConfig = {
        showPopup: {
            scale: {
                value: 1,
                time: 0.3,
                transition: 'easeOutBack',
            },
            alpha: {
                value: 1,
                time: 0.2,
                transition: 'easeOutQuad',
            },
        },
        hidePopup: {
            alpha: {
                value: 0,
                time: 0.2,
                transition: 'easeInQuad',
            },
            scale: {
                value: 0,
                time: 0.3,
                transition: 'easeInBack',
            },
        },
    };
    constructor(posY:number) {
        super();
        LayoutBuilder.create(AssetsManager.layouts.get('TurboPopup'), this);
        this.normal = this['icon'].normal;
        this.turbo = this['icon'].turbo;
        this.superTurbo = this['icon'].superTurbo;

        const slotMachine = container.resolve(SlotMachine);
        slotMachine.on(SlotMachineEvent.GAME_SPEED_LEVEL_CHANGED, this.onGameSpeedChanged, this);
        this.onGameSpeedChanged(slotMachine.currentGameSpeedLevel);

        this.position.y = posY;
    }

    private onGameSpeedChanged(level:number){
        this.normal.visible = level === 0;
        this.turbo.visible = level === 1;
        this.superTurbo.visible = level === 2;

        if(this.normal.visible) {
            this.title.text = Translation.t('turboPopup.normal');
        }

        if(this.turbo.visible) {
            this.title.text = Translation.t('turboPopup.turbo');
        }

        if(this.superTurbo.visible) {
            this.title.text = Translation.t('turboPopup.superTurbo');
        }

        autoscaleText(this.title, 30, 290, 70);
    }
}