import { Text, TextStyle } from 'pixi.js';
import { container } from 'tsyringe';
import { HoldAndSpinnerRoundResult } from '../../ignition-interactive/common/model/HoldAndSpinnerRoundResult';
import { CommonTokenConstants } from '../../ignition-interactive/common/tsyringe/tokens/CommonTokenConstants';
import { autoscaleText } from '../../ignition-interactive/core/utils/TextUtils';
import { RoundResult } from '../../ignition-interactive/slots/model/RoundResult';
import SlotMachine from '../../ignition-interactive/slots/model/SlotMachine';
import { SymbolData } from '../../ignition-interactive/slots/view/SymbolData';
import SymbolView from '../../ignition-interactive/slots/view/SymbolView';
import { SymbolsList } from '../view/SymbolsList';
import initializeMultiplier from './MultiplierUtils';

export default class ChosenSymbolView extends SymbolView{

    private multiplierStyle:TextStyle;
    public multiplier:Text;

    // There is one id "301", which should be used as 4 diff spines, depending on multipliers value
    // In Symbol list there have to be all the available SymbolData, (one with correct api id, ane the other ones can have random unused ids)
    // This is important, bcs on game start all the textures are created and assigned to each symbol data
    // In this class we are only choosing correct symbol data to pass to the SymbolView constructor
    constructor(data:SymbolData, reelIndex:number, symbolIndex:number) {
        let option: number = 0;

        if((container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap?.featureSymbolMultiplierMap) {
            (container.resolve(SlotMachine).roundResult as RoundResult & HoldAndSpinnerRoundResult).holdAndSpinnerMultiplierMap.featureSymbolMultiplierMap.forEach(m => {
                if (m.x == reelIndex && m.y == symbolIndex) {
                    container.resolve(SlotMachine).description.multiplierCompartment.some((maxValue, index) => {
                        if (maxValue > m.multiplier) {
                            option = index;
                            return true;
                        }
                    })
                }
            })
        }

        const defaultAssetName: string = 'symbol-multiplier-0';
        const searchedName:string = defaultAssetName.slice(0, -1) + option.toString();

        const newData:SymbolData = SymbolsList.find(symbolD => {
            return searchedName == symbolD.staticIcon.assetName;
        });

        super(newData);
        this.multiplierStyle ??= container.resolve(CommonTokenConstants.MULTIPLIER_STYLE)
        this.multiplier = initializeMultiplier(reelIndex, symbolIndex, this.multiplierStyle);

        if(this.multiplier) {
            this.addChild(this.multiplier);
            autoscaleText(this.multiplier, 60, this.spineAnim.width, this.spineAnim.height);
            this.multiplier.pivot.set(this.multiplier.width * 0.5, this.multiplier.height * 0.5);
        }
    }
}