import {container} from 'tsyringe';
import ControlCommand from '../../../ignition-interactive/core/control/command/ControlCommand';
import ControlEvent from '../../../ignition-interactive/core/control/event/ControlEvent';
import SlotMachine from '../../../ignition-interactive/slots/model/SlotMachine';
import { UIEventExtension } from '../../../ignition-interactive/common/control/event/UIEventExtension';


export default class AdjustBetQuantityCommand extends ControlCommand {

    public execute(event: ControlEvent): void {
        const sm: SlotMachine = container.resolve(SlotMachine);

        switch (event.type) {
            case UIEventExtension.BET_QUANTITY_UP:
                sm.betQuantity++;
                break;
            case UIEventExtension.BET_QUANTITY_DOWN:
                sm.betQuantity--;
                break;
            case UIEventExtension.BET_QUANTITY_MAX:
                sm.betQuantity = sm.description.betMaxQuantity;
                break;
        }
    }
}
